<script lang="ts" setup>
import { ref, computed, nextTick } from "vue"
import type { CSSProperties, ComputedRef } from "vue"

type POSITION = "top" | "middle" | "bottom"

const props = defineProps<{
  message: string
  icon: string
  position: POSITION
  buttonContent: string
  buttonClickEvent: Function | null
}>()

//有默认值的props
const HVAE_DEFAULT_VALUE_PROPS = ["position", "message"]
const toastWrapper = ref<HTMLElement>()
const toastContent = ref<HTMLElement>()
const toastContentOffsetWidth = ref<number>()
//tips: 向左的偏移量,是wrapper宽度的一半
const LEFT_OFFSET = ref<number>()
//tips: 向上的偏移量,主要由用户传入的props决定
const TOP_OFFSET = ref<number>()
const selfPosition: ComputedRef<number> = computed(() => {
  switch (props.position) {
    case "top":
      return 5
    case "middle":
      return 50
    case "bottom":
      return 85
  }
})

//tips: 通过获取有多少个没有默认值当props来计算出之间的空隙
//=> 设计思路: Toast的内容目前来说可能有以下几种情况
//=> 1.只有message 2.有icon和message 3.有message和button 4.三者都有(目前需求未出现2022.9.15)
//=> 其中当有buttonContent时，必须存在buttonClick事件
//=> 需要额外注意当有buttonContent时，buttonClick必定存在，要思考noDefaultValuePropsCounts的值的变化

//tips: 没有默认值的Props属性的数量
//=> 主要作用是用来搭配计算出 Toast组件的 minWidth
const noDefaultValuePropsCounts = computed<number>(() => {
  const keys = Object.keys(props)
  const haveValueProps = keys.filter((item) => props[item])
  return haveValueProps.length - HVAE_DEFAULT_VALUE_PROPS.length
})

//=> 1.当只有message时，则无需添加额外的宽度
//=> 2.当同时存在两种属性时(如message和button等情况):,需要添加额外的宽度来实现两者之间存在间距
const minWidth = computed<number>(() => {
  switch (noDefaultValuePropsCounts.value) {
    case 0: {
      return 0
    }
    case 1: {
      return toastContentOffsetWidth.value! + 10
    }
    default: {
      return toastContentOffsetWidth.value! + 20
    }
  }
})

//tips: 最外层div的样式值
const toastWrapperStyle = computed<CSSProperties>(() => {
  return {
    top: `calc(${selfPosition.value}% - ${TOP_OFFSET.value}px)`,
    left: `calc(50% - ${LEFT_OFFSET.value}px)`,
    boxShadow: `0px 2px 12px 3px rgb(202 202 202 / 30%)`,
  }
})

//tips: props传递过来的点击事件
function clickToastBtn() {
  if (!props.buttonClickEvent) return
  props.buttonClickEvent()
}

//tips: 无法在onMounted内获取offsetWidth的值
nextTick(() => {
  LEFT_OFFSET.value = toastWrapper.value!.offsetWidth / 2
  TOP_OFFSET.value = toastWrapper.value!.offsetHeight / 2
  toastContentOffsetWidth.value = toastContent.value?.offsetWidth
})
</script>

<template>
  <div>
    <div
      id="toastWrapper"
      ref="toastWrapper"
      class="absolute flex max-h-[50px] cursor-pointer justify-center rounded-[24px] border-[1px] border-solid border-[#E2E2E2] bg-[#FFFFFF] px-[22px] py-[14px]"
      :style="[toastWrapperStyle, { zIndex: 2001 }]"
    >
      <div
        ref="toastContent"
        class="flex items-center justify-between"
        :style="{ minWidth: minWidth + 'px' }"
      >
        <div v-if="props.icon" class="border border-emerald-50">
          <img
            class="h-[20px] w-[20px] object-contain pointer-events-none"
            :src="props.icon"
          />
        </div>
        <div>
          <span class="text-[#333333]">{{ message }}</span>
        </div>
        <div v-if="props.buttonContent" @click="clickToastBtn">
          <span class="text-[#E2E2E2]">{{ props.buttonContent }}</span>
          <div />
        </div>
      </div>
    </div>
  </div>
</template>

<style></style>
