import { ref } from "vue"
import { defineStore } from "pinia"

import { SortType, type SortOption } from "@/shared/use/useFileSort"

import { modeType } from "@/shared/types/type"

interface Action {
  sortOption: SortOption
  viewMode: modeType
}

interface ActionMap {
  [key: string]: Action
}

export const useActionStore = defineStore(
  "action",
  () => {
    const actionMap = ref<ActionMap>({
      global: {
        sortOption: { sortType: SortType.name, reverse: true },
        viewMode: modeType.column,
      },
      dialog: {
        sortOption: { sortType: SortType.name, reverse: true },
        viewMode: modeType.column,
      },
      search: {
        sortOption: { sortType: SortType.name, reverse: true },
        viewMode: modeType.column,
      },
    })

    const setSortOption = (opt: SortOption, id: string = "global") => {
      Object.assign(actionMap.value[id].sortOption, opt)
    }

    const setViewMode = (mode: modeType, id: string = "global") => {
      actionMap.value[id].viewMode = mode
    }

    return {
      actionMap,
      setSortOption,
      setViewMode,
    }
  },
  {
    persist: true,
  },
)
