<script setup lang="ts">
import fileIcon from "@/icons/application-blank.svg"
const props = defineProps<{
  src: string
  type: string
}>()
function onLoadError(e: Event) {
  let iconName = "none"
  if (props.type.startsWith("image")) iconName = "image"
  else if (props.type.startsWith("video")) iconName = "video"
  ;(e.target as HTMLImageElement).src = fileIcon
}
</script>

<template>
  <div class="bg-neutral-50 rounded-md aspect-square">
    <img
      :src="src"
      @error="onLoadError"
      class="square rounded-md object-cover"
    />
  </div>
</template>
