import { backendApi } from "@/shared/api/axios"

// 任务类型
export enum TaskType {
  Copy = "copy",
  Move = "move",
}

// 任务状态
export enum TaskStatus {
  Created = "created",
  Conflict = "conflict",
  Running = "running",
  Exception = "exception",
  Cancel = "cancel",
  Finished = "finished",
}

// 任务
export interface Task {
  id: number
  origin: {
    src: string[]
    dst: string
  }
  progress: {
    totalItem: number
    totalSize: number
    copiedItem: number
    copiedSize: number
    speed: number
    remain: number
  }
  status: TaskStatus
  type: TaskType
}

// 文件
export interface FileItem {
  lastModified: number
  name: string
  path: string
  size: number
}

// 可选项
export enum Solution {
  Cover = "cover",
  Skip = "skip",
  Both = "both",
  Cancel = "cancel",
  Retry = "retry",
  diy = "",
}

// 异常项
export interface ExceptionItem {
  dst: FileItem
  src: FileItem
  message: {
    type: "conflict" | "other"
    raw: string
    error: string
  }
  id: number
}

// 冲突项
export interface ConflictItem {
  dst: FileItem
  src: FileItem
  id: number
}

// 冲突解决项
export interface ConflictOption extends ConflictItem {
  rename?: string
  solution: Solution
}

// 任务参数
export interface TaskParams {
  src: string[]
  dst: string
  renames?: string[]
}

//获取用户信息
export async function getTaskList(): Promise<Task[]> {
  const response = await backendApi.get("/api/operation/list")
  return response.data
}

// 复制
export async function copy(params: TaskParams) {
  const response = await backendApi.post("/api/operation/copy", params)
  return response.data as { conflicts: ConflictItem[]; id: number }
}

// 移动
export async function move(params: TaskParams) {
  const response = await backendApi.post("/api/operation/move", params)
  return response.data as { conflicts: ConflictItem[]; id: number }
}

// 取消任务
export async function cancelTask(taskId: number) {
  const response = await backendApi.post(`/api/operation/cancel/${taskId}`)
  return response.data
}

// 获取任务异常
export async function getTaskException(taskId: number) {
  const response = await backendApi.get(`/api/operation/exception/${taskId}`)
  return response.data as { exception: ExceptionItem; id: number }
}

// 提交异常解决方案
export async function submitException(
  taskId: number,
  exceptions: ConflictOption[],
  solution?: Solution,
) {
  const params = {
    id: taskId,
    exceptions,
  }
  params["solution"] = solution
  const response = await backendApi.post(`/api/operation/exception`, params)
  return response.data
}

// 提交冲突解决方案
export async function submitConflict(
  taskId: number,
  conflicts: ConflictOption[] = [],
  solution: Solution = Solution.diy,
) {
  const response = await backendApi.post(`/api/operation/conflict`, {
    id: taskId,
    conflicts,
    solution,
  })
  return response.data
}

// 监听任务进度
export function listenTask(callback: (ev: MessageEvent<any>) => any) {
  const _prefix = window.location.protocol === "http:" ? `ws` : `wss`
  const wsHost: string = `${_prefix}://${window.location.hostname}/api/operation/message`
  const ws = new WebSocket(wsHost)
  ws.onopen = () => {
    console.info("178me-debug:", "连接ws成功")
  }
  ws.onmessage = callback
  ws.onclose = () => {
    console.info("178me-debug:", "关闭ws成功")
  }
  return ws
}

export default {
  copy,
  move,
  cancelTask,
  listenTask,
  getTaskList,
  getTaskException,
  submitException,
}
