import { TRASH_ROOT } from "@/config"
import { DiskType } from "../types/type"

import type { FileStat } from "@lazycatcloud/webdav/web"

export function getThumbnailUrl(file: FileStat, mode: DiskType, size = 150) {
  const path = encodeURIComponent(file.filename)
  switch (mode) {
    case DiskType.trashdisk:
      return `/_lzc/thumbnail/home/${TRASH_ROOT}${path}?size=${size}`
    case DiskType.offline:
    default:
      return `/_lzc/thumbnail/home${path}?size=${size}`
  }
}

export function hasThumbnail(file: FileStat, mode: DiskType): boolean {
  const mime = file.mime || ""
  return (
    [DiskType.netdisk, DiskType.trashdisk].includes(mode) &&
    (mime.startsWith("image") || mime.startsWith("video"))
  )
}

export function getIconFromMime(mime: string): string {
  const icons_path = "/mimetypes-icons"
  let iconname
  const icon_extension = ".svg"
  const icons_directory_names = [
    "directory",
    "dir",
    "folder",
    "text/directory",
    "application/folder",
  ]

  if (mime.startsWith("video")) {
    iconname = "video"
  } else if (mime.startsWith("audio")) {
    iconname = "media-audio"
  } else if (mime.startsWith("image")) {
    iconname = "image"
  } else {
    iconname = mime.split(";")[0].replace("/", "-")
  }

  if (icons_directory_names.includes(iconname)) {
    iconname = "default-folder-new"
  }

  return `${icons_path}/${iconname}${icon_extension}`
}

// 获取文件目录
export function getDirname(filename: string) {
  let result = filename.split("/")
  if (result.length === 2) return "/"
  return result.slice(0, -1).join("/")
}

// 获取文件目录
export function getBasename(filename: string) {
  let result = filename.split("/")
  return result.slice(-1)[0]
}
