//PCToast 使用方法
//1.在别的vue组件内 import PCToastCreator from "@/pc/components/Toast/PCToast"
//2.const toast = new PCToastCreator(options),
//3.必须添加一个配置对象
//{
// message:string,
// duration?:number,
// position?:string,
// icon?:string,
// buttonContent?:string,
// buttonClickEvent?:Function
//}
//4.然后调用toast.present()方法即可
//5.一般必填 message 选项,如果不填写duration和position,则采用默认值1500ms,和顶部

import { h, render } from "vue"
import Toast from "./Toast.vue"

interface ToastOptions {
  message?: string
  position?: "top" | "middle" | "bottom"
  duration?: number
  icon?: string
  buttonContent?: string
  buttonClickEvent?: Function | null
}

//配置对象的默认值
//如果不设置的话，会默认采用以下值传递给Toast组件
const DEFAULT_TOAST_OPTIONS: ToastOptions = {
  message: "懒猫云yes!!",
  position: "top",
  buttonContent: "",
  duration: 1500,
  icon: "",
  buttonClickEvent: null,
}

class PCToastCreator {
  options: ToastOptions
  container: HTMLElement
  constructor(options: ToastOptions) {
    this.options = options
    this.container = document.createElement("div")
  }

  present() {
    const lzcToast = h(h(Toast), { ...DEFAULT_TOAST_OPTIONS, ...this.options })
    render(lzcToast, this.container)
    document.body.insertBefore(this.container, document.body.firstChild)
    const durationTime = this.options.duration || DEFAULT_TOAST_OPTIONS.duration
    window.setTimeout(() => {
      this.dismiss()
    }, durationTime)
  }

  dismiss() {
    if (this.container) {
      const toastWrapperDOM = this.container.querySelector("#toastWrapper")
      toastWrapperDOM?.classList.add("animate-toastAnimation")
      window.setTimeout(() => {
        render(null, this.container)
        document.body.removeChild(this.container)
      }, 500)
    }
  }
}

export default PCToastCreator
