import { type Ref, watch } from "vue"
import { onBeforeRouteLeave } from "vue-router"
import type { VirtualList } from "@lazycatcloud/lzc-toolkit"

export const useWatchScrollTop = (
  vListRef: Ref<null | InstanceType<typeof VirtualList>>,
) => {
  onBeforeRouteLeave(() => {
    sessionStorage.setItem(
      "scrollTop",
      JSON.stringify(vListRef.value?.vListReturn.scrollTop.value || 0),
    )
  })
  const unwatch = watch(
    () => vListRef.value?.vListReturn.visiableList.value,
    () => {
      if (vListRef.value?.vListReturn.containerProps.ref.value) {
        //clientHeight可视区高度为0时,表示未渲染完成 return
        if (!vListRef.value.vListReturn.clientHeight.value) return
        // 如果totalHeight小于等于clientHeight,说明页面未超出可视区,不需要滚动,return
        if (
          vListRef.value.vListReturn.totalHeight.value <=
          vListRef.value.vListReturn.clientHeight.value
        )
          return
        unwatch()
        vListRef.value.vListReturn.containerProps.ref.value.style.scrollBehavior =
          "auto"
        vListRef.value.vListReturn.containerProps.ref.value.scrollTo({
          top: JSON.parse(sessionStorage.getItem("scrollTop") || "0"),
        })
        //跳转完后初始化,并清空scrollTop
        vListRef.value.vListReturn.containerProps.ref.value.style.scrollBehavior =
          "smooth"
        sessionStorage.setItem("scrollTop", JSON.stringify(0))
      }
    },
  )
}
