<script setup lang="ts">
import { inject, provide, ref } from "vue"
import { useSelectable } from "@lazycatcloud/lzc-toolkit"
import { computed, type Ref } from "@vue/reactivity"
import FileList from "@/mobile/components/FileList.vue"
import Breadcrumb from "@/mobile/components/Breadcrumb.vue"
import CloseIcon from "@/icons/错号.svg"
import ChooseIcon from "@/icons/选择的对号.svg"

import selected1 from "@/icons/不透明选中_mobile.svg"
import selected0 from "@/icons/不透明未选中_mobile.svg"

import { DiskType } from "@/shared/types/type"
import type { FileStat } from "@lazycatcloud/webdav"

type FilterRuleInjectFunction = (files: FileStat[]) => FileStat[]
type LoadFilesInjectFunction = (path: string) => Promise<void>

defineProps<{
  baseUrl: string
  title: string
}>()

const $$filterFiles = inject<FilterRuleInjectFunction>(
  "$$filterRuleFunction",
  (f) => f,
)

const files = inject<Ref<FileStat[]>>("$$files", ref([]))
const loadFiles = inject<LoadFilesInjectFunction>(
  "$$loadFiles",
  async (_) => {},
)
const currentPath = inject<Ref<string>>("$$currentPath", ref(""))
const isRootDir = inject<Ref<boolean>>("$$isRootDir", ref(true))

const _pickerMultiple = inject<boolean>("$$pickerMultiple")

// NOTE(d1y): 为了兼容(web componens)丑一点就丑一点吧
const pickerMultiple = computed(() => {
  const value = _pickerMultiple
  if (typeof value == "boolean") return value
  else if (typeof value == "string") {
    if (value == "false") return false
    else if (value == "true") return true
    return false
  }
  return true
})

const emit = defineEmits<{
  (e: "next", path: Array<FileStat>): void
  (e: "back"): void
}>()

// 选择模式下, 只有文件可以被选择
const selectableFiles = computed(() => {
  if (isSelectingMode.value) {
    return files.value.filter((f) => f.type == "file")
  } else {
    return files.value
  }
})

const { selected, toggle, unselectAll, isAllSelected, isSelected, toggleAll } =
  useSelectable(selectableFiles, "filename")

const isSelectingMode = ref(false)

provide("$$isSelectingMode", isSelectingMode)

const rootName = inject("$$breadcrumbRootTitle", "root")

const showFooter = computed(() => {
  return selected!.value.length > 0
})

function handleFileClick(item: FileStat) {
  if (isSelectingMode.value) {
    const selectedValue = selected.value
    if (!pickerMultiple.value && selectedValue.length == 1) {
      const hasValue = selectedValue.find((_) => _.filename == item.filename)
      if (!hasValue) return
    }
    if (item.type == "directory") return
    toggle([item])
  } else {
    // TODO(d1y): 多选
    if (item.type == "directory") {
      loadFiles(item.filename)
    } else {
      emit("next", [item])
    }
  }
}

function handleCancelClick() {
  isSelectingMode.value = false
  unselectAll!()
}

function handleFinish() {
  emit("next", selected!.value)
}
</script>

<template>
  <div @click.stop class="w-full h-full flex flex-col select-none bg-white">
    <div class="h-full flex flex-col">
      <!-- 对话框头 -->
      <div class="flex justify-between items-center my-4 px-4">
        <div class="start">
          <span
            v-if="isSelectingMode"
            class="font-bold text-[14px] text-[#2EC1CC]"
            @click="handleCancelClick"
          >
            取消
          </span>
          <div v-else @click="emit('back')">
            <img
              class="pointer-events-none"
              name="错号"
              color="black"
              :width="14"
              :height="14"
              :src="CloseIcon"
            />
          </div>
        </div>
        <div class="center">
          <div
            v-if="isSelectingMode"
            :class="isAllSelected ? `mr-[-28px]` : `static`"
          >
            <span class="text-center font-bold">
              已选择{{ selected!.length }}个文件
            </span>
          </div>
          <div v-else>{{ title }}</div>
        </div>

        <div
          class="end"
          :style="{
            opacity: pickerMultiple ? 1 : 0,
            pointerEvents: pickerMultiple ? 'all' : 'none',
          }"
        >
          <span
            v-if="isSelectingMode"
            @click="pickerMultiple && toggleAll!()"
            class="font-bold text-[14px] text-[#2EC1CC]"
          >
            {{ isAllSelected ? "取消全选" : "全选" }}
          </span>
          <div v-else @click="isSelectingMode = true">
            <img
              :src="ChooseIcon"
              name="选择的对号"
              color="black"
              :width="20"
              :hight="20"
              class="pointer-events-none"
            />
          </div>
        </div>
      </div>
      <div class="breadcrumb">
        <Breadcrumb
          v-if="!isRootDir"
          v-model="currentPath"
          @click="handleFileClick"
          :rootName="rootName"
        />
        <!-- <FileSortBar /> -->
      </div>
      <!-- 文件列表 -->
      <div class="flex grow overflow-hidden">
        <FileList
          class="file-list"
          v-bind:class="{ selecting: isSelectingMode }"
          :list="$$filterFiles!(files)"
          :isLoading="false"
          :showAdd="false"
          :mode="DiskType.netdisk"
          :isSelected="isSelected"
          @itemClick="handleFileClick"
        >
          <template #end="{ item }">
            <img
              @click="item.type == 'file' && toggle!([item])"
              v-show="isSelectingMode"
              :src="isSelected!(item) ? selected1 : selected0"
            />
          </template>
        </FileList>
      </div>
      <!-- 底部 -->
      <div
        class="px-[58px] h-[44px] bottom-[13px] w-full bg-white"
        v-if="showFooter"
      >
        <div
          @click="handleFinish"
          class="w-full h-full bg-[#2EC1CC] rounded-[22px] text-[#FFFFFF] font-medium text-[14px] leading-[44px] text-center"
        >
          确定
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
.file-list.selecting :deep(.directory) {
  opacity: 0.6;
}
</style>
