import { computed, unref, type Ref } from "vue"

import { useActionStore } from "@/shared/store/action"
import dayjs from "dayjs"

import type { FileStat } from "@lazycatcloud/webdav/web"

enum StrType {
  Symbols,
  Chinese,
  Letter,
  Number,
}

export enum SortType {
  name = "name",
  time = "time",
  type = "type",
  size = "size",
  content = "content",
}

type SortActionMap = Record<
  SortType,
  {
    name: string
    actionName: string
  }
>

export let sortActionMap: SortActionMap = {
  name: { actionName: "按名称排序", name: "文件名" },
  time: { actionName: "按时间排序", name: "修改时间" },
  size: { actionName: "按大小排序", name: "文件大小" },
  type: { actionName: "按类型排序", name: "文件类型" },
  content: { actionName: "按内容排序", name: "文件内容" },
}

export interface SortOption {
  sortType?: SortType
  // 默认为升序
  reverse?: boolean
}

const mimeTypes = ["text", "image", "video", "audio", "application"]

function sortByType(a: FileStat, b: FileStat) {
  const value1 = a.type
  const value2 = b.type
  return value1 > value2 ? 1 : value1 === value2 ? 0 : -1
}

export function than(val1: number | string, val2: typeof val1) {
  let result: number
  if (typeof val1 === "number" && typeof val2 === "number") {
    result = val1 - val2
  } else if (typeof val1 === "string" && typeof val2 === "string") {
    result = val1.localeCompare(val2, undefined, {
      usage: "sort",
      numeric: true,
    })
  } else {
    result = String(val1).localeCompare(String(val2), undefined, {
      usage: "sort",
      numeric: true,
    })
  }
  return result
}

// 获取字符类型
function getStringType(str: string) {
  if (/^\+?[0-9][0-9]*$/.test(str)) return StrType.Number
  else if (/[a-z]/i.test(str)) return StrType.Letter
  else if (/[\u4e00-\u9fa5]/i.test(str)) return StrType.Chinese
  //特殊字符
  // if (
  //   /[\!\@\#\$\%\^\&\*\~\`(\){\}\<\>\[\]\|\/\\\,\;\'\"\?\_\-\+\=]/.test(str)
  // )
  return StrType.Symbols
}

// 自然排序
export function natsort(a: string, b: string) {
  //获取2个字符串最短的
  //设置如果相比前面字符都一样，那么则最短的调换位置
  var len = a.length,
    re = -1
  if (a.length > b.length) {
    //获取最短的那个字符串长度
    len = b.length
    re = 1
  }
  //循环对每个字符串遍历对比
  for (var i = 0; i < len; i++) {
    var n1 = getStringType(a[i]) //获取当前字符的类型
    var n2 = getStringType(b[i])
    if (a[i] === b[i] && n1 !== StrType.Number) continue
    if (n1 > n2) return -1
    else if (n1 < n2)
      //不同类型的，换相反位置
      return 1
    else {
      if (n1 === StrType.Number) {
        //数字的时候的比较方式，是获取后面的全部数字对比
        var num1 = parseInt(a.substring(i))
        var num2 = parseInt(b.substring(i))
        //如果数字相等，那么也跳过不对比
        if (num1 === num2) continue
        return num1 - num2
      } else {
        return than(a[i], b[i])
      }
    }
  }
  return re
}

const sortFnMap = {
  name: (a: FileStat, b: FileStat) => {
    return natsort(a.basename, b.basename)
  },
  time: (a: FileStat, b: FileStat) => {
    return than(
      dayjs(a.lastmod).format("YYYY-MM-DD HH:mm"),
      dayjs(b.lastmod).format("YYYY-MM-DD HH:mm"),
    )
  },
  size: (a: FileStat, b: FileStat) => than(a.size, b.size),
  type: (a: FileStat, b: FileStat) => {
    const result = than(
      mimeTypes.findIndex((it) => a.mime?.includes(it)),
      mimeTypes.findIndex((it) => b.mime?.includes(it)),
    )
    if (result === 0 && a.mime && b.mime) return natsort(a.mime, b.mime)
    return result
  },
  content: (a: FileStat, b: FileStat) => {
    if (a["lines"]) return than(a["lines"].join(""), b["lines"].join(""))
    return natsort(a.basename, b.basename)
  },
}

export default function useFileSort(
  fileList: Array<FileStat> | Ref<Array<FileStat>>,
  actionId: string = "global",
) {
  const actionStore = useActionStore()

  const sortOption = computed(() => {
    return actionStore.actionMap[actionId].sortOption
  })

  const reverse = computed(() => {
    return sortOption.value.reverse ? 1 : -1
  })

  // 当前排序方法
  const sortFn = computed(() => {
    return (a: FileStat, b: FileStat) => {
      // 先进行文件夹排序
      const sortByTypeResult = sortByType(a, b)
      if (sortByTypeResult !== 0) return sortByTypeResult * reverse.value
      // 如果类型相同执行当前排序方法
      if (sortOption.value.sortType) {
        const sortFnResult = sortFnMap[sortOption.value.sortType](a, b)
        if (sortFnResult !== 0) return sortFnResult * reverse.value
      }
      // 如果相同则按照名称排序决定
      return sortFnMap.name(a, b)
    }
  })

  const sortFileList = computed(() => {
    // console.info("178me-debug:排序前", toRaw(unref(fileList)))
    let list = [...unref(fileList)]
    list.sort(sortFn.value)
    list.sort(sortByType)
    // console.info("178me-debug:排序后", toRaw(unref(fileList)))
    return list
  })
  return {
    sortMap: sortFnMap,
    sortFileList,
  }
}
