<script setup lang="ts">
import { computed, inject } from "vue"

import { modeType } from "@/shared/types/type"
import { useActionStore } from "@/shared/store/action"

import res1 from "./列表模式_pc.svg"
import res2 from "./网格模式_pc.svg"

const props = withDefaults(
  defineProps<{
    viewModes?: modeType[]
    actionId?: string
  }>(),
  {
    viewModes: () => [modeType.column, modeType.grid, modeType.gutter],
    actionId: "global",
  },
)

const viewModes = inject("viewModes", props.viewModes)

let viewModeMap = {}
viewModeMap[`${modeType.column}`] = { name: res1 }
viewModeMap[`${modeType.grid}`] = { name: res2 }

const actionStore = useActionStore()
const currentViewMode = computed(() => {
  return actionStore.actionMap[props.actionId].viewMode
})

function hdlClick(viewMode: modeType) {
  actionStore.setViewMode(viewMode, props.actionId)
}
</script>
<template>
  <div class="flex space-x-[2px]">
    <div
      @click="hdlClick(viewMode)"
      v-for="viewMode in viewModes"
      class="p-[7px] item-hover"
    >
      <img
        class="w-[14px] h-[14px]"
        :style="{
          filter: currentViewMode == viewMode ? '' : 'grayscale(1)',
        }"
        :src="viewModeMap[viewMode].name"
      />
    </div>
  </div>
</template>
