import { ref, unref, computed } from "vue"
import { defineStore } from "pinia"
import path from "path-browserify"

import {
  LzcWebdavClient,
  getWebdavRootFromType,
} from "@/shared/utils/lzcWebdavClient"
import lzcAPIGateway from "@/shared/utils/lzcApiGatewayProxy"
import LzcToastCreator from "@/mobile/components/toast/lzcToast"
import { useHistoryTrashStore } from "@/shared/store/historyTrashStore"
import { TRASH_ROOT } from "@/config"
import { getDirname } from "@/shared/utils/file"
import { useTask } from "@/shared/store/task"

import type { FileStat } from "@lazycatcloud/webdav"
import { DiskType } from "@/shared/types/type"
import { TaskType } from "@/shared/api/taskManage"
import { useDelayedLoading } from "../use/useDelayedLoading"
import type { ConflictMap } from "@/shared/use/task/useTaskConflict"
import { trashPut, trashRm } from "@/shared/api/fileOperations"

export const useScopedWebdav = () => {
  const trashStore = useHistoryTrashStore()
  const taskStore = useTask()

  const gateWay = lzcAPIGateway
  const client = ref<LzcWebdavClient>()
  const files = ref<FileStat[]>([])
  const isLoading = ref(false)
  const { isLoading: isDelayedLoading } = useDelayedLoading(isLoading)
  const currentDir = ref("/") //其实就是 ==> fileStat.filename
  const zipPath = ref("")
  const modeType = ref(DiskType.netdisk)

  const isRootDir = computed(() => {
    return currentDir.value === "/"
  })

  async function initClient(mode: DiskType) {
    //避免重复初始化
    if (client.value && mode === modeType.value) return client.value
    modeType.value = mode
    files.value = []
    const rootUrl = await getWebdavRootFromType(mode)
    if (rootUrl) {
      client.value = new LzcWebdavClient(rootUrl)
      // 兼容多盘符webdav(windows)
      if (mode !== DiskType.localdevice) client.value.ensureDirExist("/")
    } else {
      console.error("no such disk type")
    }
    return client.value
  }

  // 初始化
  async function init(mode: DiskType, initDir: string = "/") {
    //避免重复初始化
    if (client.value && mode === modeType.value) return client.value
    await initClient(mode)
    await loadDir(initDir)
    return client.value
  }

  //加载页面内容
  async function loadDir(dir: string = currentDir.value) {
    isLoading.value = true
    files.value = []
    try {
      currentDir.value = dir
      files.value = await client.value!.getDirectoryContents(dir)
    } catch (e) {
      console.log(e)
    } finally {
      isLoading.value = false
    }
  }

  function loadPreviousDir() {
    const previousDir = getDirname(currentDir.value)
    loadDir(previousDir)
    return previousDir
  }

  //创建文件夹
  async function createDir(name: string) {
    const dirName = unref(name)
    const path = currentDir.value + "/" + dirName
    try {
      await client.value!.client!.createDirectory(path)
    } catch (e: any) {
      if (e.status === 405) {
        throw new Error("已经存在相同的文件夹")
      } else {
        console.error(e)
      }
    }
    loadDir()
  }

  //复制文件或文件夹
  async function copyFiles(files: FileStat[], dst: string) {
    return await taskStore.add({ src: files, dst: dst }, TaskType.Copy)
  }

  //移动文件或文件夹
  async function moveFiles(files: FileStat[], dst: string) {
    return await taskStore.add({ src: files, dst: dst }, TaskType.Move)
  }

  //重命名
  async function renameFile(file: FileStat, newName: string) {
    const src = file.filename
    const dest = path.resolve(getDirname(file.filename), newName)
    try {
      await client.value!.client!.moveFile(src, dest)
      new LzcToastCreator({ message: "重命名成功" }).present()
    } catch (e: any) {
      throw new Error(e)
    }
    loadDir()
  }

  // 删除网盘文件(移动到回收站)
  async function deleteFiles(files: FileStat[]) {
    //如果没有.Trash,创建.Trash文件夹
    const hasTrash = await client.value?.client!.exists(".Trash")
    if (!hasTrash) createDir(".Trash")
    //移动文件到TRASH_ROOT中,直接移动文件夹就可以把文件夹里面的内容转移出去
    const historyFiles = files.map((item) => ({
      srcPath: item.filename,
      trashPath: `${TRASH_ROOT}/_${new Date().getTime()}_${item.basename}`,
    }))
    await trashStore.putHistoryPath(historyFiles)
    await trashPut(
      historyFiles.map((f) => ({ src: f.srcPath, dest: f.trashPath })),
    )
    new LzcToastCreator({ message: "删除成功" }).present()
    await loadDir()
  }

  // 恢复文件
  async function recoverFiles(files: FileStat[]) {
    let conflictMap: ConflictMap = {}
    const { historyFileMap } = await trashStore.getHistoryByFiles(files)
    for (const k in historyFileMap) {
      const res = await taskStore.add(
        {
          src: historyFileMap[k],
          dst: k,
          renames: historyFileMap[k].map((it) => it.basename),
        },
        TaskType.Move,
      )
      if (res && res.conflicts?.length > 0)
        conflictMap[`${res.id}`] = res.conflicts
    }
    return conflictMap
  }

  // 删除离线文件
  async function deleteOfflineFiles(files: FileStat[]) {
    await client.value?.deepDeleteFiles(files)
    await loadDir()
  }

  // 删除网盘文件
  async function deepDeleteFiles(files: FileStat[]) {
    await trashRm(files.map((it) => it.filename))
    await loadDir()
  }

  // 清空回收站
  async function clearRecycleBin(files: FileStat[]) {
    await trashRm(files.map((it) => it.filename))
    trashStore.clearAllHistoryPath()
    await loadDir()
  }

  // 主要是预览页面需要通过路由传递 filename 去获取当前文件的整个 stat
  async function getFileStat(filename: string) {
    if (filename === "/") return
    isLoading.value = true
    const fileStat = await client.value!.getFileStatByFilename(filename)
    isLoading.value = false
    return fileStat
  }

  // 获取当前文件的下载链接
  function getFileDownloadLink(filename: string) {
    const downloadLink = client.value!.getDownloadLink(filename)
    return downloadLink
  }

  // 获取文件夹的内容
  async function getDirContent(dirFilename: string, options: object = {}) {
    const dirContent = await client.value!.getDirectoryContents(
      dirFilename,
      options,
    )
    return dirContent
  }

  // 获取文档类型文件的字符串内容信息
  async function getFileContens(
    filename: string,
    options: object = { format: "text" },
  ) {
    const fileContents = await client.value!.getFileContents(filename, options)
    return fileContents
  }

  function loadZip(zipFilename: string) {
    let isFirst: boolean = true
    const mountDirName = ".$$_lzc-zipPre" + Date.now() // 确保挂载名不会产生重名
    const zipReader = gateWay.pd.MountArchive({
      srcPath: zipFilename,
      mountpoint: mountDirName,
    })
    zipReader.subscribe(async () => {
      if (isFirst) {
        isLoading.value = true
        files.value = []
        try {
          zipPath.value = zipFilename
          files.value = await client.value!.getDirectoryContents(mountDirName)
        } catch (e) {
          console.log(e)
        } finally {
          isLoading.value = false
          isFirst = false
        }
      }
    })
  }

  return {
    // 变量
    files,
    modeType,
    client,
    currentDir,
    isLoading,
    isDelayedLoading,
    isRootDir,
    // 下面的是方法
    init,
    initClient,
    loadDir,
    loadZip,
    loadPreviousDir,
    createDir,
    copyFiles,
    moveFiles,
    renameFile,
    deleteFiles,
    deleteOfflineFiles,
    recoverFiles,
    clearRecycleBin,
    deepDeleteFiles,
    getFileStat,
    getFileDownloadLink,
    getFileContens,
    getDirContent,
  }
}

export const useWebdavStore = defineStore("webdav", useScopedWebdav)
