<script lang="ts">
export default {
  inheritAttrs: false,
}
</script>
<script setup lang="ts">
import {
  ref,
  computed,
  type CSSProperties,
  inject,
  watch,
  onUnmounted,
} from "vue"
import {
  isSelectedKey,
  isAllSelectedKey,
  allSelectedKey,
  toggleKey,
  toggleAllKey,
  VirtualList,
  ScrollBar,
} from "@lazycatcloud/lzc-toolkit"

import { useWatchScrollTop } from "@/pc/components/FileList/use/useWatchScrollTop"
import MimeIcon from "@/shared/components/MimeIcon.vue"
import ImageIcon from "@/shared/components/ImageIcon.vue"
import Checkbox from "@/pc/components/Checkbox/index.vue"
import FileInput from "@/pc/components/Dialog/FileFolderPicker/FileInput.vue"
import EmptyDir from "./components/EmptyDir.vue"

import { useBestLayout } from "@/pc/use/useBestLayout"
import { toChunk } from "@/shared/utils/builder"
import { getThumbnailUrl, hasThumbnail } from "@/shared/utils/file"
import { useDragSelectByFileList } from "./use/useDragSelect"

import type { FileStat } from "@lazycatcloud/webdav/web"
import type { DiskType } from "@/shared/types/type"

const props = withDefaults(
  defineProps<{
    files: FileStat[]
    mode: DiskType
    showCheckbox?: boolean
    // 是否需要自定义右键菜单
    showMenuList?: boolean
    //是否需要头部的全选栏
    showSelectBox?: boolean
    // 是否使用默认的 checked 事件
    defaultChecked?: boolean
    // 是否使用默认的全选事件
    defualtSelectedAll?: boolean
    // 是否使用默认的滚动监听事件
    defaultScrollChanged?: boolean
    // 是否允许拖转选择
    canDragSelect?: boolean
  }>(),
  {
    showCheckbox: true,
    showMenuList: true,
    showSelectBox: true,
    defaultChecked: true,
    defualtSelectedAll: true,
    defaultScrollChanged: true,
    canDragSelect: true,
  },
)
const emit = defineEmits([
  "fileClick",
  "dirClick",
  "zipClick",
  "checkboxClick",
  "focusout",
  "allClick",
])

const contentWrapper = ref<HTMLDivElement>()
const defaultValues = {
  height: 132,
  width: 132,
  rowGap: 12, // item左右之间的间距
  textMargin: 12, // item上下之间的间距
}

const vListRef = ref<null | InstanceType<typeof VirtualList>>(null)
if (props.defaultScrollChanged) useWatchScrollTop(vListRef)
//监听vListRef的滚动位置,退出时还原
const { isClick } = useDragSelectByFileList(vListRef, props.canDragSelect)
//处理新建文件夹逻辑
const isCreateNewDir = inject("isCreateNewDir", ref(false))
const createNewDir = inject("createNewDir", (_: string) => {})
const cancelCreate = () => {
  isCreateNewDir.value = false
}

//tips: 根据屏幕宽度获取最佳的item显示大小和每行显示的大小
const { bestWidth, bestRowCounts } = useBestLayout(
  contentWrapper,
  defaultValues.width,
  defaultValues.rowGap,
)

// 选择逻辑
const isSelected = inject(isSelectedKey, () => false)
const isAllSelected = inject(isAllSelectedKey)
const allSelected = inject(allSelectedKey)
const toggle = inject(toggleKey, () => {})
const toggleAll = inject(toggleAllKey, () => {})

// 已选择文件的数量提示
const selectedTip = computed(() => {
  return allSelected && allSelected?.value.length > 0
    ? `已选择: ${allSelected?.value.length}个文件`
    : null
})
const getItemHeight = () => {
  return bestWidth.value! + defaultValues.textMargin
}

const wrapperStyle = computed<CSSProperties>(() => {
  return {
    display: `grid`,
    gridTemplateColumns: `repeat(${bestRowCounts.value},${bestWidth.value}px)`,
    gap: `${defaultValues.rowGap}px`,
  }
})

const files = computed(() => {
  const newArr: FileStat[] = [
    {
      filename: "", // 无需真实
      basename: "", // 无需真实
      lastmod: new Date().toISOString(),
      size: -1,
      type: "directory",
      etag: null,
    },
  ]

  if (isCreateNewDir.value) {
    return newArr.concat(props.files)
  } else {
    return props.files
  }
})

const fileslist = computed(() => {
  // NOTICE by default bestRowCounts will be 0, this cause memory leak
  if (bestRowCounts.value > 1) {
    return toChunk(files.value, bestRowCounts.value)
  }
  return []
})

const hdClickCheckbox = (item: FileStat) => {
  // 如果组件 prop 中提供了 defaultChecked 属性，意味着需要默认选中该文件，在加载组件时进行切换
  if (props.defaultChecked) {
    toggle([item])
  }
  emit("checkboxClick", item)
}

// 发送文件点击和文件夹点击事件
const hdlClickItem = (item: FileStat) => {
  const MIME_ZIP = ["application/zip", "application/x-7z-compressed"]
  if (!isCreateNewDir.value) {
    if (item.mime && MIME_ZIP.includes(item.mime)) {
      emit("zipClick", item)
    } else if (item.type === "directory") {
      emit("dirClick", item)
    } else emit("fileClick", item)
  }
}

const handleClickAllButton = () => {
  if (props.defualtSelectedAll) {
    toggleAll()
  }
  emit("allClick")
}

// 选中样式
const itemClass = computed(
  () => (checked: boolean) =>
    Object({
      "item-selected": checked,
      "item-hover": !checked,
    }),
)
</script>
<template>
  <EmptyDir v-if="files.length === 0" />
  <div v-else class="flex flex-col grow" v-bind="$attrs">
    <div
      v-if="showSelectBox"
      class="h-[40px] w-full flex items-center select-none"
    >
      <div
        class="flex items-center cursor-pointer"
        @click="handleClickAllButton"
      >
        <Checkbox
          :checked="isAllSelected!"
          @click.stop="handleClickAllButton"
          class="mr-[12px]"
        />
        <span class="text-[#B5B5B5] text-[12px]">
          {{ selectedTip ? selectedTip : "全部文件" }}
        </span>
      </div>
    </div>
    <div ref="contentWrapper" class="relative grow select-none pr-[12px]">
      <ScrollBar
        v-if="vListRef"
        :container-el="vListRef.vListReturn.containerProps.ref.value"
        auto-hide
      />
      <VirtualList
        ref="vListRef"
        class="absolute top-0 left-0"
        :data="fileslist"
        :itemHeight="getItemHeight"
        v-slot="{ data }"
      >
        <div :style="wrapperStyle">
          <div
            v-for="item in data"
            :filename="item.filename"
            :key="item.filename"
          >
            <div
              :style="{
                height: bestWidth + 'px',
                width: bestWidth + 'px',
              }"
            >
              <div
                @click="hdClickCheckbox(item)"
                class="w-full h-full relative p-[16px] group select-none rounded-[4px]"
                :class="itemClass(isSelected(item))"
              >
                <div
                  v-if="showCheckbox"
                  class="absolute rounded-[2px] flex items-center justify-center top-[6px] right-[6px] w-[12px] h-[12px] cursor-pointer opacity-0 group-hover:opacity-100"
                  :class="isSelected(item) ? 'opacity-100 ' : 'opacity-0 '"
                >
                  <Checkbox
                    @click.stop="hdClickCheckbox(item)"
                    :checked="isSelected(item)"
                  />
                </div>
                <div
                  class="mx-auto"
                  :style="{
                    width: 'calc(100% - 36px)',
                  }"
                >
                  <div
                    @click.stop
                    @mousedown.left="isClick = true"
                    @mousemove="isClick = false"
                    @mouseup="isClick && hdlClickItem(item)"
                  >
                    <ImageIcon
                      :src="getThumbnailUrl(item, mode)"
                      :type="item.mime"
                      alt=""
                      class="h-full w-full mr-[20px] rounded-md object-cover"
                      v-if="hasThumbnail(item, mode)"
                    />
                    <mime-icon
                      v-else
                      class="h-full w-full rounded-md object-cover"
                      :draggable="false"
                      :mime="item?.mime! || item?.type!"
                    />
                  </div>
                </div>
                <div
                  class="baseNameBox h-[40px] mt-[6px] text-center text-[14px] text-[#333333] leading-[20px]"
                >
                  <div v-if="item.size == -1" class="w-full h-[22px]">
                    <FileInput
                      :files="files"
                      @focusout="createNewDir"
                      @input-esc="cancelCreate"
                    />
                  </div>
                  <div v-else @click.stop="hdlClickItem(item)">
                    {{ item.basename }}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </VirtualList>
    </div>
  </div>
</template>
<style scoped>
.baseNameBox {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2; /* 这里是超出几行省略 */
  font-weight: 600;
  word-break: break-all;
  text-overflow: ellipsis;
  overflow: hidden;
}
</style>
