import { onUnmounted, ref, watch, type Ref } from "vue"
import { useDragSelectByPc as useDragSelect } from "@/shared/use/useDragSelect"
import type { VirtualList } from "@lazycatcloud/lzc-toolkit"

export function useDragSelectByFileList(
  vListRef: Ref<null | InstanceType<typeof VirtualList>>,
  selectMode: boolean,
) {
  const drag = useDragSelect({ attribute: "filename" })
  const isClick = ref(false)
  if (selectMode) {
    const unwatch = watch(vListRef, () => {
      if (!vListRef.value) return
      unwatch()
      drag.init(vListRef.value.vListReturn.containerProps.ref)
    })
    onUnmounted(() => {
      drag.unMount()
    })
  }
  return { isClick }
}
