<script lang="ts" setup>
import { ref, computed, nextTick } from "vue"

import type { CSSProperties, ComputedRef } from "vue"

type POSITION = "top" | "middle" | "bottom"

const props = defineProps<{
  message: string
  icon: string
  position: POSITION
  buttonClickEvent: Function | null
}>()
/**
 * NOTE:
 * => 需要判断如何居中对齐
 */

const toastWrapper = ref<HTMLElement>()
//tips: 向左的偏移量,是wrapper宽度的一半
const LEFT_OFFSET = ref<number>()
//tips: 向上的偏移量,主要由用户传入的props决定
const TOP_OFFSET = ref<number>()
const selfPosition: ComputedRef<number> = computed(() => {
  switch (props.position) {
    case "top":
      return 10
    case "middle":
      return 50
    case "bottom":
      return 85
  }
})

//tips: props传递过来的点击事件
function clickToastBtn() {
  if (!props.buttonClickEvent) return
  props.buttonClickEvent()
}

const toastWrapperStyle = computed<CSSProperties>(() => {
  return {
    top: `calc(${selfPosition.value}% - ${TOP_OFFSET.value}px)`,
    left: `calc(50% - ${LEFT_OFFSET.value}px)`,
    backgroundColor: `black`,
  }
})

/**
 * NOTE: 动态计算出自身宽度来控制居中对齐
 */
nextTick(() => {
  LEFT_OFFSET.value = toastWrapper.value!.offsetWidth / 2
  TOP_OFFSET.value = toastWrapper.value!.offsetHeight / 2
})
</script>

<template>
  <div @click.stop="clickToastBtn">
    <div
      id="toastWrapper"
      ref="toastWrapper"
      class="absolute rounded-[8px] opacity-[0.8] border-x-[16px] border-y-[14px] h-[44px] border-[black] bg-black/80"
      :style="[toastWrapperStyle, { zIndex: 9999 }]"
    >
      <div class="flex max-w-[700px] h-full items-center justify-center">
        <div>
          <span class="text-white text-[14px]">{{ message }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<style></style>
