import { backendApi } from "./axios"

export async function trashPut(
  items: Array<{
    src: string
    dest: string
  }>,
) {
  await backendApi.post("/api/operation/trash_put", {
    pathes: items,
  })
}

export async function trashRm(pathes: Array<string>) {
  await backendApi.post("/api/operation/trash_rm", {
    pathes,
  })
}
