<script setup lang="ts">
import MimeIcon from "@/shared/components/MimeIcon.vue"
import { formatBytes } from "@/shared/utils/formatBytes"
import type { FileStat } from "@lazycatcloud/webdav"
import CloseIcon from "@/icons/错号.svg"
import { computed, inject } from "vue"

const props = defineProps<{
  items: FileStat[]
  target?: string
}>()

const emit = defineEmits<{
  (e: "back"): void
  (e: "finish", payload: FileStat[]): void
}>()

const $rootProps = inject<any>("$$props")

const calcTitle = computed(() => {
  const { title, confirmTitle } = $rootProps?.value || {}
  if (!!confirmTitle) return confirmTitle
  return title
})

const calcButtonTitle = computed<string>(() => {
  const { confirmButtonTitle } = $rootProps?.value || {}
  if (!!confirmButtonTitle) return confirmButtonTitle as string
  return "确定"
})

const displayMime = computed(() => {
  const item = props.items[0]
  return item.mime || item.type
})

const totalSize = computed(() => {
  if (props.items[0].type == "directory") return 0
  return formatBytes(
    props.items.reduce((s, p) => {
      return s + p.size
    }, 0),
    2,
  )
})

const itemsDesc = computed(() => {
  const target = props.items[0]
  const basename: string = target.basename
  const folderPrefix = target.type == "directory" ? "夹" : ""
  if (props.items.length > 1) {
    return `${basename}等${props.items.length}个文件${folderPrefix}`
  } else {
    return basename
  }
})
</script>

<template>
  <div class="w-full h-full relative">
    <div class="h-[25px] p-[20px] flex items-center justify-between">
      <div @click="emit('back')">
        <img
          :src="CloseIcon"
          class="pointer-events-none"
          width="14"
          color="#333333"
        />
      </div>
      <span class="text-[18px] font-semibold text-[#202020]">{{
        calcTitle
      }}</span>
      <div class="w-[14px] h-full"></div>
    </div>

    <div class="w-full h-full text-center mt-[30%]">
      <MimeIcon
        class="h-[80px] w-[80px] object-cover mx-auto"
        :mime="displayMime"
      />
      <div class="h-[12px]"></div>
      <p class="max-w-[420px] w-[80%] px-[24px] bg-white break-all mx-auto">
        {{ itemsDesc }}
      </p>
      <p v-if="!!totalSize">{{ totalSize }}</p>
      <div
        class="mt-[24px] text-[16px] h-[20px] max-w-[90%] mx-auto truncate leading-[22px] text-[#333333] font-semibold"
      >
        <!-- 来自：{{ choiceItems[0].basename }} -->
      </div>
    </div>

    <div
      class="w-full px-[36px] mt-[20px] text-[#999999] text-center leading-[22px] mulLineTruncate"
      v-if="target"
    >
      保存路径：{{ target }}
    </div>
    <div class="fixed px-[58px] h-[44px] bottom-[13px] w-full bg-white">
      <div
        @click="emit('finish', items)"
        class="w-full h-full bg-[#2EC1CC] rounded-[22px] text-[#FFFFFF] font-medium text-[14px] leading-[44px] text-center"
      >
        {{ calcButtonTitle }}
      </div>
    </div>
  </div>
</template>
