<script lang="ts" setup>
import { onMounted, ref, inject, computed, provide } from "vue"
import { useSelectable } from "@lazycatcloud/lzc-toolkit"
import { useDebounceFn } from "@vueuse/core"

import ListView from "@/pc/components/FileList/ListView.vue"
import GridView from "@/pc/components/FileList/GridView.vue"
import Breadcrumb from "@/pc/components/Breadcrumb/index.vue"
import ViewModeGroup from "./ViewModeGroup.vue"
import LzcToastCreator from "@/mobile/components/toast/lzcToast"
import Loading from "@/pc/components/Loading/index.vue"

import { filenameRule } from "@/shared/utils/filenameRule"
import useFileSort from "@/shared/use/useFileSort"
import { useScopedWebdav } from "@/shared/store/webdav"
import { useActionStore } from "@/shared/store/action"

import { modeType, DiskType } from "@/shared/types/type"
import type { FileStat } from "@lazycatcloud/webdav"

import { useFilter } from "../useFilter"

interface IProps {
  confirmTitle?: string
  cancelTitle?: string
  breadcrumbRoot?: string
  isDir?: boolean
  multiple?: boolean
  rules?: string
}

const kIgnoreRule = "-1"

const actionStore = useActionStore()
const currentViewMode = computed(() => {
  return actionStore.actionMap["dialog"].viewMode
})

const props = withDefaults(defineProps<IProps>(), {
  confirmTitle: "确定",
  cancelTitle: "取消",
  breadcrumbRoot: "全部文件",
  isDir: true,
  multiple: false,
  rules: "-1",
})

const isCreateNewDir = ref(false)

let webdav = useScopedWebdav()

const isMultiple = computed(() => {
  return props.multiple
})

const { currentDir, client, loadDir, isLoading, files: originFiles } = webdav

const createNewDir = async (e: string) => {
  const { conformRules, message } = filenameRule(e)
  if (conformRules) {
    const path = currentDir.value + "/" + e
    const isExist = await client.value?.isExist(path)
    if (isExist) {
      new LzcToastCreator({ message: "创建失败,存在同名文件" }).present()
    } else {
      await client.value?.createDir(e)
      loadDir(currentDir.value)
    }
  } else {
    new LzcToastCreator({
      message: message,
    }).present()
  }
  isCreateNewDir.value = false
}
provide("isCreateNewDir", isCreateNewDir)
provide("createNewDir", createNewDir)

const parentRules = inject("$$rules", "*")

function isDirByFileStat(file: FileStat) {
  return file.type === "directory"
}

const rules = computed(() => {
  const { rules: _ } = props
  if (_ == kIgnoreRule) {
    return parentRules
  }
  return _
})

const files = computed(() => {
  if (props.isDir) {
    return originFiles.value.filter(isDirByFileStat)
  }
  return originFiles.value
})
const {
  selected,
  toggle,
  toggleAll,
  select,
  unselect,
  unselectAll,
  isSelected,
} = useSelectable<FileStat, "filename">(files, "filename")
const { sortFileList } = useFileSort(files, "dialog")

const { list: filterFiles } = useFilter(sortFileList, rules)

const closeDialog = () => {
  emit("back")
}

const emit = defineEmits(["submit", "back"])

// copy by ChatGPT
function compareObjects<T>(obj1: T, obj2: T, keyname: keyof T) {
  return obj1[keyname] === obj2[keyname]
}
function isSubset<T>(arr1: T[], arr2: T[], keyname: keyof T) {
  for (let i = 0; i < arr1.length; i++) {
    const obj1 = arr1[i]
    const obj2 = arr2.find((obj) => compareObjects(obj, obj1, keyname))
    if (!obj2) {
      return false
    }
  }
  return true
}
// ===============

const handleDirClick = (file: FileStat) => {
  unselectAll()
  loadDir(file.filename)
}
const handleCheckbox = (file: FileStat) => {
  if (isDirByFileStat(file) && !props.isDir) return
  const _is = isSelected(file)
  const onceItem = [file]
  if (!isMultiple.value) {
    unselectAll()
    if (_is) {
      unselect(onceItem)
    } else {
      select(onceItem)
    }
  } else toggle(onceItem)
}

const handleClickAllButton = () => {
  const isDir = props.isDir
  if (isDir) {
    toggleAll()
  } else {
    const _ = filterFiles.value.filter((e) => !isDirByFileStat(e))
    // FIXME(d1y): subset not impl :(
    const checkLength = selected.value.length == _.length
    const subsetFlag = isSubset<FileStat>(selected.value, _, "filename")
    const isSelectedAll = subsetFlag && checkLength
    if (!selected.value.length || !isSelectedAll) {
      select(_)
      return
    }
    unselectAll()
  }
}
const handleFileClick = (file: FileStat) => {
  handleCheckbox(file)
}
const fileTotalNum = computed(() => {
  return files.value.length
})
onMounted(() => {
  webdav.init(DiskType.netdisk)
})
const handleConfrim = useDebounceFn(async () => {
  const data = selected.value
  if (!data.length) {
    if (props.isDir) {
      try {
        const data = await client.value?.client?.stat(currentDir.value)
        emit("submit", [data])
      } catch (error) {
        console.log({ error })
      }
    }
    return
  }
  const parseData: FileStat[] = JSON.parse(JSON.stringify(data))
  emit("submit", parseData)
}, 400)

defineExpose({
  // FIXME(d1y): 只会初始化一次(<remove onMounted hook>)
  init() {
    // TODO(d1y): 使用上层带过来的路径
    loadDir("/")
    unselectAll()
  },
})
</script>

<template>
  <div
    class="h-[584px] w-[900px] bg-white rounded-t-[8px] roun m-auto flex flex-col border select-none overflow-hidden"
  >
    <div class="flex items-center justify-between h-[65px] w-full px-[20px]">
      <Breadcrumb
        :fileTotalNum="fileTotalNum"
        :rootName="breadcrumbRoot"
        :current-dir="currentDir"
        @click="handleDirClick"
      />
      <ViewModeGroup
        :viewModes="[modeType.column, modeType.grid]"
        action-id="dialog"
      />
    </div>
    <div class="border-b-[#F6F6F6] border-b-[1px]" />

    <Loading v-if="isLoading" />

    <template v-else>
      <ListView
        v-if="currentViewMode === modeType.column"
        :files="filterFiles"
        :mode="DiskType.netdisk"
        @file-click="handleFileClick"
        @dir-click="handleDirClick"
        @checkbox-click="handleCheckbox"
        @all-click="handleClickAllButton"
        :show-checkbox="true"
        :show-menu-list="true"
        :default-checked="false"
        :showSelectBox="isMultiple"
        :defualt-selected-all="false"
        :default-scroll-changed="false"
        :can-drag-select="false"
        action-id="dialog"
        class="px-[20px]"
      >
      </ListView>
      <GridView
        v-else-if="currentViewMode === modeType.grid"
        :files="filterFiles"
        :mode="DiskType.netdisk"
        @file-click="handleFileClick"
        @dir-click="handleDirClick"
        @checkbox-click="handleCheckbox"
        @all-click="handleClickAllButton"
        :default-checked="false"
        :show-checkbox="true"
        :show-menu-list="true"
        :showSelectBox="isMultiple"
        :defualt-selected-all="false"
        :default-scroll-changed="false"
        :can-drag-select="false"
        class="px-[20px]"
      >
      </GridView>
    </template>
  </div>

  <div
    class="h-[76px] w-full bg-[#F7F8F9] rounded-b-[8px] flex items-center justify-between px-[20px]"
  >
    <div></div>
    <div class="flex text-[14px]">
      <button
        @click="closeDialog"
        class="cursor-pointer rounded-[18px] py-[8px] px-[32px] font-medium bg-[#F7F8F9]"
      >
        {{ cancelTitle }}
      </button>
      <button
        @click="handleConfrim"
        class="cursor-pointer rounded-[18px] py-[8px] px-[32px] font-medium bg-[#2EC1CC] text-white"
      >
        {{ confirmTitle }}
      </button>
    </div>
  </div>
</template>
