import { join } from "path-browserify"
import lzcApi from "@/shared/utils/lzcApiGatewayProxy"
import type { EndDeviceProxy } from "@lazycatcloud/sdk"
import type { EndDevice } from "@lazycatcloud/sdk/dist/common/end_device"

let device: EndDeviceProxy
let currentDevice: EndDevice | undefined

let url: string = ""
let rootDir: string = ""
let deviceApiUrl: string = ""

// 获取当前终端的信息
async function _getCurrentEndDevice(): Promise<EndDevice | undefined> {
  let s = await lzcApi.session
  for (let d of (await lzcApi.devices.ListEndDevices({ uid: s.uid })).devices) {
    if (s.deviceId == d.uniqueDeivceId) {
      return d
    }
  }
  return
}

async function getLocalWebdavUrl() {
  const endDevice = await _getCurrentEndDevice()

  if (endDevice) {
    deviceApiUrl = endDevice.deviceApiUrl + "/s/files/"
    return endDevice.deviceApiUrl.replace(".d.", ".local.") + "/s/files/"
  }
  return ""
}

async function asyncInit() {
  if (!currentDevice) {
    currentDevice = await _getCurrentEndDevice()
  }
  if (!device) {
    device = await lzcApi.currentDevice
  }
  if (!url) {
    url = await getLocalWebdavUrl()
  }
  if (!rootDir) {
    let deviceInfo = await device.device.Query({})
    rootDir = deviceInfo.documentRootDir
  }
}

export async function getCurrentEndDevice() {
  await asyncInit()
  return currentDevice
}

export const getWebdavUrl = async () => {
  await asyncInit()
  return url
}

export const getWebdavRootDir = async () => {
  await asyncInit()
  return rootDir
}

// 获取本地设备url
export const getDeviceApiUrl = async () => {
  const currentDevice = await lzcApi.currentDevice
  const deviceInfo = await currentDevice.device.Query({})
  await getLocalWebdavUrl()
  switch (deviceInfo.OS) {
    case "android":
      return join(deviceApiUrl + deviceInfo.documentRootDir)
    case "ios":
      return join(deviceApiUrl + deviceInfo.documentRootDir)
    default:
      return deviceApiUrl
  }
}
