import { LzcWebdavClient } from "@/shared/utils/lzcWebdavClient"
import type { FileStat } from "@lazycatcloud/webdav/web"

import { ref } from "vue"
export function useWebdavNavigate() {
  const currentPath = ref("/")
  const files = ref<FileStat[]>([])
  const isRootDir = ref(true)
  let client: LzcWebdavClient | undefined

  async function createClient(baseUrl: string) {
    client = new LzcWebdavClient(baseUrl)
    loadFiles("/")
    return client
  }

  async function loadFiles(path: string) {
    files.value = await client!.getDirectoryContents(path)
    currentPath.value = path
    isRootDir.value = client!.isRootDir
  }

  return {
    createClient,
    loadFiles,
    files,
    currentPath,
    isRootDir,
  }
}
