import { computed, defineComponent, h, ref, render, type Ref } from "vue"

import { useSelectableInjectors } from "@lazycatcloud/lzc-toolkit"
import { useThrottleFn } from "@vueuse/shared"

interface DragOptions {
  attribute: string
  selectWay?: "row" | "colum"
  scrollOptions?: {
    margin?: number
    startSpeed?: number
    maxSpeed?: number
  }
}

type ContainsRefType = Ref<HTMLElement | null | undefined>
export function useDragSelect({
  attribute,
  selectWay = "colum",
  scrollOptions = {},
}: DragOptions) {
  // 选择相关的方法
  const { select, unselect, unselectAll } = useSelectableInjectors()
  // 选择的item
  let selectItem: Record<string, any> = {}
  const selectFn = computed(() =>
    selectWay === "colum"
      ? useThrottleFn(columSelect, 50)
      : useThrottleFn(rowSelect, 50),
  )
  // 当前选择集合 (增量选项)
  const currentSelectSet = new Set()
  // 第一次拖拽 (用于非增量选择拖拽时取消选择全部)
  const firstDrag = ref(true)

  // 容器元素
  const containerEl: ContainsRefType = ref()
  // 是否拖动中
  const isPressing = ref(false)
  // 开始点
  const startPoint = ref({ x: 0, y: 0 })
  // 上一点
  const lastPoint = ref({ x: 0, y: 0, scrollTop: 0 })
  // 监听容器变化并更新
  const observer = new ResizeObserver(
    (entries) =>
      (containerRect.value = entries[0].target.getBoundingClientRect()),
  )
  // 容器的范围
  const containerRect = ref<DOMRect>({} as DOMRect)
  // 容器的滚动范围
  const scrollRect = ref({
    top: 0,
    height: 0,
    sync(mode: boolean = false) {
      scrollRect.value.top = containerEl.value?.scrollTop || 0
      if (mode) scrollRect.value.height = containerEl.value?.scrollHeight || 0
    },
  })
  // 选择范围
  const selectRange = computed(() => {
    if (!isPressing.value) return { left: 0, top: 0, width: 0, height: 0 }
    return {
      left:
        Math.min(startPoint.value.x, lastPoint.value.x) -
        containerRect.value.left,
      top:
        Math.min(startPoint.value.y, lastPoint.value.y) -
        containerRect.value.top,
      width: Math.abs(startPoint.value.x - lastPoint.value.x),
      height: Math.abs(startPoint.value.y - lastPoint.value.y),
    }
  })

  /* ------------------ 自动滚动相关 ----------------- */
  const { margin = 50, startSpeed = 30, maxSpeed = 30 } = scrollOptions
  // 自动滚动计时器ID
  let autoScrollTimer: ReturnType<typeof requestAnimationFrame>
  // 触发自动滚动的Margin
  const AUTOSCROLLMARGIN = margin
  // 起始滚动速度
  const STARTSPEED = startSpeed
  // 最大滚动速度
  const MAXSCROLLSPEED = maxSpeed
  // 缓动函数
  const EASING = (t: number) => t ** 1
  let scrollSpeed = 0

  function autoScroll(y: number) {
    cancelAnimationFrame(autoScrollTimer)
    scrollSpeed = calcScrollSpeed(y)
    // 限制最大滚动速度
    scrollSpeed = Math.max(
      Math.min(scrollSpeed, MAXSCROLLSPEED),
      -MAXSCROLLSPEED,
    )
    if (scrollSpeed !== 0) {
      const container = containerEl.value
      if (!container) return
      if (!isPressing.value) return
      const maxScrollTop = scrollRect.value.height - containerRect.value.height
      let scrollTop = scrollRect.value.top + Number(scrollSpeed)
      scrollTop = Math.min(Math.max(0, scrollTop), maxScrollTop)
      container.scrollTop = scrollTop
      setScrollRange()
      autoScrollTimer = requestAnimationFrame(() => autoScroll(y))
    }
  }

  function calcScrollSpeed(y: number) {
    const container = containerEl.value
    if (container) {
      // 首先判断是否靠近边缘,然后使用缓动函数计算速度
      const _containerRect = containerRect.value
      const distanceFromBottom = _containerRect.bottom - y
      if (distanceFromBottom < AUTOSCROLLMARGIN) {
        return STARTSPEED * EASING(1 - distanceFromBottom / AUTOSCROLLMARGIN)
      }
      const distanceFromTop = y - _containerRect.top
      if (distanceFromTop < AUTOSCROLLMARGIN) {
        return -STARTSPEED * EASING(1 - distanceFromTop / AUTOSCROLLMARGIN)
      }
    }
    return 0
  }

  // 选择框和子元素是否相交
  function isIntersect(childRect: DOMRect) {
    const parentRect = { ...selectRange.value, right: 0, bottom: 0 }
    parentRect.left += containerRect.value.left
    parentRect.top += containerRect.value.top - scrollRect.value.top
    parentRect.right = parentRect.width + parentRect.left
    parentRect.bottom = parentRect.height + parentRect.top
    return (
      parentRect.left < childRect.right &&
      parentRect.right > childRect.left &&
      parentRect.top < childRect.bottom &&
      parentRect.bottom > childRect.top
    )
  }

  function setScrollRange() {
    // 计算公式: 上次的y - (当前scrllTop - 上次记录的scrollTop)
    scrollRect.value.sync()
    const { top, height: maxHeight } = scrollRect.value
    lastPoint.value.y = Math.min(
      maxHeight + containerRect.value.top,
      lastPoint.value.y + top - lastPoint.value.scrollTop,
    )
    lastPoint.value.scrollTop = top
  }

  function addCssRule(
    selector: string,
    rule: string,
    styleSheetId = "dynamic-styles",
  ): void {
    let styleSheet = document.getElementById(styleSheetId) as HTMLStyleElement
    if (!styleSheet) {
      styleSheet = document.createElement("style")
      styleSheet.id = styleSheetId
      document.head.appendChild(styleSheet)
    }
    const rules = styleSheet.sheet!.cssRules
    const index = rules.length
    const serializedRule = `${selector} { ${rule} }`
    for (let i = 0; i < index; i++) {
      const existingRule = rules[i] as CSSStyleRule
      if (
        existingRule.selectorText === selector &&
        existingRule.style.cssText === rule
      ) {
        return
      }
    }
    styleSheet.sheet!.insertRule(serializedRule, index)
  }

  /* ---------------------------------------------------- */

  function dragStart(x: number, y: number) {
    firstDrag.value = true
    isPressing.value = true
    // 如果 scrollHeight 说明未被初始化过,需要更新top,height
    if (!scrollRect.value.height) scrollRect.value.sync(true)
    else scrollRect.value.sync()
    startPoint.value.x = x
    startPoint.value.y = y + scrollRect.value.top
    lastPoint.value.x = startPoint.value.x
    lastPoint.value.y = startPoint.value.y
    // NOTE: 禁用平滑滚动(不禁用会影响我们的自动滚动功能,巨坑)
    containerEl.value?.classList.add("disable-scroll-behavior")
    // NOTE: 初始化当前选择的Item(增量选择)
    currentSelectSet.clear()
  }

  function dragMove(x: number, y: number, hook?: Function) {
    if (!isPressing.value) return
    if (firstDrag.value && selectWay === "colum" && !isSingleClick()) {
      unselectAll()
      firstDrag.value = false
    }
    hook?.()
    autoScroll(y)
    // x坐标不得小于容器的left
    lastPoint.value.x = Math.max(x, containerRect.value.left)
    // y坐标需要加上滚动后的距离 最大不能超过滚动高度
    lastPoint.value.y = Math.min(
      y + scrollRect.value.top,
      scrollRect.value.height + containerRect.value.top,
    )
    // 记录scrollTop，用于在滚动时间记录
    lastPoint.value.scrollTop = scrollRect.value.top
    // 切换选择状态
    selectFn.value()
  }

  function dragEnd() {
    isPressing.value = false
    containerEl.value?.classList.remove("disable-scroll-behavior")
  }

  function onScroll() {
    if (!isPressing.value) return
    if (!scrollSpeed) {
      setScrollRange()
    }
    selectFn.value()
  }

  /* ---------------------------------------------------- */

  // 防止点拖情况
  function isSingleClick() {
    if (!isPressing.value) return false
    const { width, height } = selectRange.value
    if (Math.max(width, height) > 5) return false
    return true
  }

  // 对外提供点击取消选择功能(主要是PC使用)
  function cancelSelectAllBySingleClick() {
    if (isSingleClick()) unselectAll()
  }

  function columSelect() {
    if (isSingleClick()) return
    // 列模式选择 相交的选择,不相交的不选择
    containerEl.value?.querySelectorAll(`[${attribute}]`).forEach((it) => {
      selectItem[`${attribute}`] = it.attributes[`${attribute}`].value
      if (isIntersect(it.getBoundingClientRect())) {
        select([selectItem])
      } else {
        unselect([selectItem])
      }
    })
  }

  function rowSelect() {
    // 行模式选择 选择相交的最小和最大之间所有
    const vals: string[] = []
    let minIndex: any = null
    let maxIndex = 0
    containerEl.value
      ?.querySelectorAll(`[${attribute}]`)
      .forEach((it, index) => {
        const val = it.attributes[`${attribute}`].value
        vals.push(val)
        if (isIntersect(it.getBoundingClientRect())) {
          if (minIndex === null) minIndex = index
          minIndex = Math.min(minIndex, index)
          maxIndex = Math.max(maxIndex, index)
        } else {
          selectItem[`${attribute}`] = val
          if (currentSelectSet.has(val)) unselect([selectItem])
        }
      })
    for (let i = minIndex; i < maxIndex + 1; i++) {
      selectItem[`${attribute}`] = vals[i]
      select([selectItem])
      currentSelectSet.add(vals[i])
    }
  }

  // 初始化容器
  function init(_containerEl: ContainsRefType) {
    if (!_containerEl?.value) return false
    containerEl.value = _containerEl.value
    if (!containerEl?.value) return false
    observer.observe(containerEl.value)
    addCssRule(".disable-scroll-behavior", "scroll-behavior: unset !important;")
    return true
  }

  return {
    // fn
    init,
    dragStart,
    dragMove,
    dragEnd,
    onScroll,
    isSingleClick,
    cancelSelectAllBySingleClick,
    selectFn,
    // var
    selectRange,
    isPressing,
    containerEl,
    observer,
  }
}

export function useDragSelectByPc(options: DragOptions) {
  const drag = useDragSelect(options)
  const { observer, selectRange } = drag
  const {
    onScroll,
    dragStart,
    dragMove,
    dragEnd,
    cancelSelectAllBySingleClick,
  } = drag
  // 选择框的样式
  const compStyle = computed(() => {
    return {
      left: `${selectRange.value.left}px`,
      top: `${selectRange.value.top}px`,
      width: `${selectRange.value.width}px`,
      height: `${selectRange.value.height}px`,
    }
  })
  // 选择框组件
  const selectBoxComp = defineComponent({
    render() {
      return h("div", {
        id: "selectBoxEl",
        class: "absolute z-50 bg-blue-200 opacity-50",
        style: compStyle.value,
      })
    },
  })

  function mouseDown(event: MouseEvent) {
    // 左键才允许拖拽
    if (event.button !== 0) return
    dragStart(event.x, event.y)
  }

  function mouseMove(event: MouseEvent) {
    dragMove(event.x, event.y)
  }

  function mouseUp(event: MouseEvent) {
    // 是否在目标元素中
    const atTargetRect = event
      .composedPath()
      .some((it) => (it as HTMLElement)?.getAttribute?.(options.attribute))
    if (!atTargetRect) cancelSelectAllBySingleClick()
    dragEnd()
  }

  function init(_containerEl: ContainsRefType) {
    // 添加监听器
    if (!drag.init(_containerEl)) return
    if (!drag.containerEl?.value) return false
    render(h(selectBoxComp), drag.containerEl.value)

    drag.containerEl.value?.addEventListener("scroll", onScroll)
    drag.containerEl.value?.addEventListener("mousedown", mouseDown)
    window.addEventListener("mousemove", mouseMove)
    window.addEventListener("mouseup", mouseUp)
  }

  function unMount() {
    // 移除监听器
    observer.disconnect()
    drag.containerEl.value?.removeEventListener("scroll", onScroll)
    drag.containerEl.value?.removeEventListener("mousedown", mouseDown)
    window.removeEventListener("mousemove", mouseMove)
    window.removeEventListener("mouseup", mouseUp)
  }
  return { init, unMount }
}

export function useDragSelectByMobile(
  options: DragOptions,
  joinSelectMode?: Function,
  pressTime: number = 600,
) {
  const drag = useDragSelect({
    ...options,
    selectWay: "row",
    scrollOptions: {
      startSpeed: 5,
      maxSpeed: 5,
    },
  })
  const { observer } = drag
  const { onScroll, dragStart, dragMove, dragEnd, selectFn } = drag
  let pressTimer: ReturnType<typeof setTimeout> | undefined

  function cancelTimer() {
    clearTimeout(pressTimer)
  }

  function touchStart(event: TouchEvent) {
    // 不允许重复触摸
    cancelTimer()
    pressTimer = setTimeout(() => {
      const { clientX, clientY } = event.targetTouches[0]
      dragStart(clientX, clientY)
      joinSelectMode?.()
      selectFn.value()
      event.preventDefault()
    }, pressTime)
  }

  function touchMove(event: TouchEvent) {
    cancelTimer()
    const { clientX, clientY } = event.targetTouches[0]
    dragMove(clientX, clientY, () => event.preventDefault())
  }

  function touchEnd() {
    cancelTimer()
    dragEnd()
  }

  function init(_containerEl: ContainsRefType) {
    // 添加监听器
    if (!drag.init(_containerEl)) return
    if (!drag.containerEl?.value) return false

    drag.containerEl.value?.addEventListener("scroll", onScroll)
    drag.containerEl.value?.addEventListener("touchstart", touchStart)
    drag.containerEl.value?.addEventListener("touchmove", touchMove)
    window.addEventListener("touchcancel", touchEnd)
    window.addEventListener("touchend", touchEnd)
  }

  function unMount() {
    // 移除监听器
    observer.disconnect()
    drag.containerEl.value?.removeEventListener("scroll", onScroll)
    drag.containerEl.value?.removeEventListener("touchstart", touchStart)
    drag.containerEl.value?.removeEventListener("touchmove", touchMove)
    window.removeEventListener("touchcancel", touchEnd)
    window.removeEventListener("touchEnd", touchEnd)
  }
  return { init, unMount }
}
