<script lang="ts" setup>
import { computed, ref, onUpdated } from "vue"

import path from "path-browserify"

const props = defineProps<{
  modelValue: string
  rootName: string
  searchPath?: string
}>()

const emit = defineEmits(["update:modelValue", "click"])

const nav = ref<HTMLDivElement | null>(null)

const rootDir = "/"

const navPaths = computed(() => {
  if (!!props.searchPath) {
    const fullPath = props.modelValue // props 传来的始终是当前的 filename(完整路径)
    const fromPath = props.searchPath // 从路由拿到起点路径
    const basicPath = fromPath.split("/").slice(0, -1).join("/") //记录之前的路径
    const showCrumbs: string[] = fullPath
      .replace(basicPath, "")
      .split("/")
      .filter((item) => !!item)
    const arr = showCrumbs.map((item, index, arr) => {
      const _name = arr.slice(0, index + 1).join("/")
      const _fullpath = path.join(basicPath, _name)
      return {
        name: item,
        path: _fullpath,
      }
    })
    return arr
  } else {
    const arr = props.modelValue
      .split("/")
      .filter((p) => p)
      .map((n, i, a) => {
        const filePath = a.slice(0, i + 1).join("/")
        return {
          name: n,
          path: path.join("/", filePath),
        }
      })
    return arr
  }
})

const handleRootClick = () => {
  emit("click", {
    type: "directory",
    filename: rootDir,
    basename: props.rootName,
  })
}

const handleNavClick = (p) => {
  emit("click", {
    type: "directory",
    filename: path.join(rootDir, p.path),
    basename: p.name,
    from: "search",
  })
}

const scrollEnd = () => {
  const scrollWidth = nav.value?.scrollWidth
  nav.value?.scrollTo(scrollWidth!, 0)
}

onUpdated(() => {
  scrollEnd()
})
</script>

<template>
  <div
    ref="nav"
    class="ml-[20px] pl-[20px] px-[8px] mr-[20px] text-[#999] text-[14px] h-[44px] bg-[#F7F8F9] rounded-[10px] flex items-center overflow-auto"
  >
    <div v-show="!searchPath" class="flex items-center whitespace-nowrap">
      <div @click="handleRootClick" class="max-w-[200px] overflow-hidden">
        {{ rootName }}
      </div>

      <svg
        class="mx-[8px]"
        width="6px"
        height="10px"
        viewBox="0 0 6 10"
        version="1.1"
        xmlns="http://www.w3.org/2000/svg"
        xmlns:xlink="http://www.w3.org/1999/xlink"
      >
        <title>路径</title>
        <g
          id="页面-1"
          stroke="none"
          stroke-width="1"
          fill="none"
          fill-rule="evenodd"
        >
          <g
            id="2操作-移动-复制"
            transform="translate(-102.000000, -121.000000)"
            fill="#999999"
            fill-rule="nonzero"
          >
            <g id="定位" transform="translate(20.000000, 104.000000)">
              <g id="路径-3" transform="translate(82.000000, 17.000000)">
                <path
                  d="M1.50072819,9 C1.37256719,9 1.2444062,8.94429939 1.14696147,8.83289817 C0.951012843,8.60888485 0.951012843,8.24804178 1.14696147,8.02402846 L3.79173838,5.00045408 L1.14696147,1.97687971 C0.951012843,1.75286639 0.951012843,1.39202331 1.14696147,1.16800999 C1.3429101,0.94399667 1.65854627,0.94399667 1.8544949,1.16800999 L4.85303853,4.59601922 C5.04898716,4.82003254 5.04898716,5.18087562 4.85303853,5.40488894 L1.8544949,8.83289817 C1.75705018,8.94429939 1.62888918,9 1.50072819,9 Z"
                  id="路径"
                ></path>
              </g>
            </g>
          </g>
        </g>
      </svg>
    </div>
    <div class="flex-1 flex items-center overflow-x-auto">
      <!-- whitespace-nowrap 用来处理字段之间的空格 -->
      <div
        v-for="(p, index) in navPaths"
        class="breadcrumb flex items-center whitespace-nowrap"
      >
        <div @click="handleNavClick(p)" class="max-w-[180px] truncate">
          {{ String(p.name) }}
        </div>

        <svg
          v-if="index < navPaths.length - 1"
          class="mx-[8px] separator"
          width="6px"
          height="10px"
          viewBox="0 0 6 10"
          version="1.1"
          xmlns="http://www.w3.org/2000/svg"
          xmlns:xlink="http://www.w3.org/1999/xlink"
        >
          <title>路径</title>
          <g
            id="页面-1"
            stroke="none"
            stroke-width="1"
            fill="none"
            fill-rule="evenodd"
          >
            <g
              id="2操作-移动-复制"
              transform="translate(-102.000000, -121.000000)"
              fill="#999999"
              fill-rule="nonzero"
            >
              <g id="定位" transform="translate(20.000000, 104.000000)">
                <g id="路径-3" transform="translate(82.000000, 17.000000)">
                  <path
                    d="M1.50072819,9 C1.37256719,9 1.2444062,8.94429939 1.14696147,8.83289817 C0.951012843,8.60888485 0.951012843,8.24804178 1.14696147,8.02402846 L3.79173838,5.00045408 L1.14696147,1.97687971 C0.951012843,1.75286639 0.951012843,1.39202331 1.14696147,1.16800999 C1.3429101,0.94399667 1.65854627,0.94399667 1.8544949,1.16800999 L4.85303853,4.59601922 C5.04898716,4.82003254 5.04898716,5.18087562 4.85303853,5.40488894 L1.8544949,8.83289817 C1.75705018,8.94429939 1.62888918,9 1.50072819,9 Z"
                    id="路径"
                  ></path>
                </g>
              </g>
            </g>
          </g>
        </svg>
      </div>
    </div>
  </div>
</template>

