<script setup lang="ts">
import { computed, provide, ref, type CSSProperties, nextTick } from "vue"
import type { FileStat } from "@lazycatcloud/webdav/web"

import ChooseFiles from "./ChooseFiles.vue"
import ChooseDirectories from "./ChooseDirectories.vue"
import Confirm from "./Confirm.vue"
import { FilePickerBoxFitType } from "../types"

import filterFiles from "../useFilter"

import { useWebdavNavigate } from "./useWebdavNavigate"
import type { LzcWebdavClient } from "@/shared/utils/lzcWebdavClient"
import { isMobile } from "@/shared/utils/isMobile"
import FileFolderPicker from "@/webComponents/PC/FileFolderPicker.vue"

export interface IFilePickerProps {
  /**
   * 是否显示<filepicker>
   * 目前需要配合 v-if 来使用
   */
  show?: boolean

  /**
   * 默认为根目录: `/_lzc/files/home`
   */
  baseUrl?: string

  /**
   * 确认上传的标题
   * 不存在则使用默认的标题
   */
  confirmTitle?: string

  /**
   * 确认上传按钮的标题
   * 不存在则使用默认的确定
   */
  confirmButtonTitle?: string

  /**
   * 标题
   */
  title: string
  target?: string

  /**
   * 类型 <文件 / 文件夹>
   */
  type?: "file" | "directory"

  /**
   * 是否多选
   */
  multiple?: boolean

  /**
   * 允许选择的文件类型(字符串类型)
   * > 默认不传递参数或者为 <false | all | *> 魔法值则显示全部文件
   * 通过传递 ',' 分隔符传递多个匹配规则
   */
  accept?: string

  /**
   * dialog渲染大小
   */
  boxFitType?: FilePickerBoxFitType
  /** 若boxFitType为fill则以下参数不可用 */
  width?: string
  height?: string

  /*
   * 面包屑根目录标题
   */
  breadcrumbRootTitle?: string
}
const fileFolderPickerInstance = ref<InstanceType<typeof FileFolderPicker>>()
const { createClient, loadFiles, files, currentPath, isRootDir } =
  useWebdavNavigate()

let $$client: LzcWebdavClient | null = null

provide("$$client", $$client)

provide("$$files", files)
provide("$$loadFiles", loadFiles)
provide("$$currentPath", currentPath)
provide("$$isRootDir", isRootDir)

let dialogInstance: any = null

const props = withDefaults(defineProps<IFilePickerProps>(), {
  type: "directory",
  title: "",
  show: false,
  multiple: false,
  target: "",
  boxFitType: FilePickerBoxFitType.fill,
  width: "",
  height: "",
  accept: "*",
  confirmTitle: "",
  confirmButtonTitle: "确定",
  baseUrl: "/_lzc/files/home",
  breadcrumbRootTitle: "root",
})

async function beforeHook(stepUrl: string = "") {
  let baseUrl = !!stepUrl ? stepUrl : props.baseUrl
  $$client = await createClient(baseUrl)
}

provide("$$breadcrumbRootTitle", props.breadcrumbRootTitle)

provide(
  "$$props",
  computed(() => {
    const { title, confirmTitle, confirmButtonTitle } = props
    return {
      title,
      confirmTitle,
      confirmButtonTitle,
    }
  }),
)

provide("$$rules", props.accept)

provide("$$pickerMultiple", props.multiple)

const emit = defineEmits<{
  (e: "submit", data: Array<FileStat> | FileStat): void
  (e: "update:show", show: boolean): void
  (e: "visible", show: boolean): void
}>()

const widgetShow = ref<boolean>(false)

function $filterFiles(files: FileStat[]) {
  return filterFiles(files, props.accept)
}

provide("$$filterRuleFunction", $filterFiles)

const filePickerStyle = computed<CSSProperties>(() => {
  const { boxFitType, width, height } = props
  const obj: CSSProperties = {
    width,
    height,
  }
  if (boxFitType == FilePickerBoxFitType.fill) {
    Object.keys(obj).map((key) => {
      obj[key] = "100%"
    })
  }
  obj.display = widgetShow.value ? "block" : "none"
  obj.background = isMobile() ? "white" : ""
  return obj
})

const hideDialog = () => {
  emit("update:show", false)
  emit("visible", false)
}

const isConfirmMode = ref(false)
const confirmItems = ref<FileStat[]>([])

function toConfirmStep(items: FileStat[]) {
  isConfirmMode.value = true
  confirmItems.value = items
}

async function handleSubmit(items: FileStat[] | FileStat | string) {
  if (!items) return
  let output: FileStat[] | FileStat = []
  let parseFlag = true
  if (typeof items == "string") {
    parseFlag = false
    let target = await $$client?.client?.stat(items)
    if (!!target) {
      if ("data" in target) {
        target = target.data
      }
      output.push(target)
    }
  }
  if (parseFlag) {
    output = JSON.parse(JSON.stringify(items)) as FileStat[]
  }
  if (!props.multiple) {
    output = output[0]
  }
  emit("submit", output)
  hideDialog()
}

function handleSubmitWithPC(data: any) {
  const multiple = props.multiple
  let result: FileStat[] | FileStat = data
  if (!multiple) {
    result = data[0]
  }
  const _ = JSON.parse(JSON.stringify(result))
  emit("submit", _)
}

defineExpose({
  async init(initPath: string = "") {
    if (!!isMobile()) {
      isConfirmMode.value = false
      confirmItems.value = []
      files.value = []
      await beforeHook(initPath)
    } else {
      await nextTick()
      fileFolderPickerInstance.value?.init()
    }
  },
  isOpened() {
    if (isMobile()) {
      return widgetShow.value
    }
    return !!dialogInstance
  },
  open() {
    widgetShow.value = true
  },
  close() {
    widgetShow.value = false
  },
  toggle() {
    if (this.isOpened()) this.close()
    else this.open()
  },
})
</script>

<template>
  <div
    class="fixed bottom-0 left-0 flex flex-grow z-[999999]"
    :style="filePickerStyle"
  >
    <template v-if="isMobile()">
      <Confirm
        v-if="isConfirmMode"
        :items="confirmItems"
        @finish="handleSubmit"
        @back="isConfirmMode = false"
      />
      <template v-else>
        <ChooseFiles
          :base-url="baseUrl"
          :title="title"
          v-if="type == 'file'"
          @back="hideDialog"
          @next="toConfirmStep"
        />
        <ChooseDirectories
          v-else
          :base-url="baseUrl"
          :title="title"
          @next="toConfirmStep"
          @finish="handleSubmit"
          @back="hideDialog"
        />
      </template>
    </template>
    <template v-else>
      <div
        class="modal absolute inset-0 bottom-0 z-50 flex bg-stone-900 bg-opacity-50"
      >
        <div class="dialog m-auto flex flex-col">
          <FileFolderPicker
            ref="fileFolderPickerInstance"
            :is-dir="type != 'file'"
            :multiple="multiple"
            :rules="accept"
            :confirm-title="confirmButtonTitle"
            @submit="handleSubmitWithPC"
            @back="hideDialog"
          />
        </div>
      </div>
    </template>
  </div>
</template>
<style>
@import "./index.css";

.mulLineTruncate {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 15;
  overflow: hidden;
}
</style>
