<script setup lang="ts">
import { computed, inject, type ComputedRef } from "vue"
import SvgIcon from "@/shared/components/SvgIcon.vue"
import { sortActionMap, SortType } from "@/shared/use/useFileSort"
import { useSelectableInjectors } from "@lazycatcloud/lzc-toolkit"

import sortSVG from "@/icons/排序方向_pc.svg"

import { useActionStore } from "@/shared/store/action"

const props = defineProps<{
  colHeaders: SortType[]
  actionId: string
}>()

// Store
const actionStore = useActionStore()
const sortOption = computed(() => {
  return actionStore.actionMap[props.actionId].sortOption
})

const gridTemplateColumns = inject<ComputedRef<string>>("$gridInjectStyle")

// 选择逻辑
const { selected } = useSelectableInjectors()

// 已选择文件的数量提示
const selectedTip = computed(() => {
  return selected && selected?.value.length > 0
    ? `已选择: ${selected?.value.length}个文件`
    : null
})
// 排序方向
const sortDirection = computed(() => {
  return !sortOption.value.reverse
})

// 是否当前排序方法
const isCurrentSortAction = (sortType: SortType) =>
  sortOption.value.sortType === sortType

// 切换排序方向
function hdlClickColumnHeader(sortType: SortType) {
  if (isCurrentSortAction(sortType)) {
    actionStore.setSortOption(
      {
        reverse: !sortOption.value?.reverse,
      },
      props.actionId,
    )
  } else {
    actionStore.setSortOption({ sortType, reverse: true }, props.actionId)
  }
}

function showSelectedTip(sortType: SortType) {
  return sortType === props.colHeaders[0] && selectedTip.value
}
</script>
<template>
  <div
    :style="{
      'grid-template-columns': gridTemplateColumns,
    }"
    class="listView w-full h-[40px] items-center text-[#B5B5B5] text-[12px]"
  >
    <div
      @click="!selectedTip && hdlClickColumnHeader(sortType)"
      v-for="sortType in colHeaders"
      class="flex items-center space-x-[4px] cursor-pointer"
    >
      <span v-if="showSelectedTip(sortType)" class="shrink-0">
        {{ selectedTip }}
      </span>
      <span v-else class="shrink-0">{{ sortActionMap[sortType]?.name }}</span>
      <img
        class="w-[6px] h-[10px] shrink-0"
        :style="{
          transform: `rotate(${sortDirection ? 180 : 0}deg)`,
        }"
        v-if="!selectedTip && isCurrentSortAction(sortType)"
        :src="sortSVG"
      />
    </div>
  </div>
</template>
