<script lang="ts" setup>
import { useDebounceFn } from "@vueuse/shared"
import { ref, computed, onMounted } from "vue"

import BreadcrumbItem from "./components/BreadcrumbItem.vue"

const props = defineProps<{
  currentDir: string
  fileTotalNum: number
  rootName?: string
}>()
const emit = defineEmits(["click"])

const isHide = ref(false)
const nav = ref<HTMLDivElement | null>(null)
const childPath = computed(() => {
  if (props.currentDir === "/") return []
  let _paths = props.currentDir.split("/")
  return _paths.slice(1, _paths.length)
})
const visiableList = ref<number[]>([])

const calcVisiableList = useDebounceFn(() => {
  if (!nav.value) return
  nav.value.scrollTo(9999999999, 0)
  const children = nav.value.children
  const rect = nav.value.getBoundingClientRect()
  const _visiableList: number[] = []
  for (let i = children.length - 1; i >= 0; i--) {
    const it = children[i]
    if (rect.left <= it.getBoundingClientRect().left) {
      _visiableList.push(parseInt(it.getAttribute("data-index") || "0"))
    } else {
      isHide.value = true
      break
    }
  }
  // 当子项存在时 至少需要有一个可见
  if (!_visiableList.length && childPath.value.length)
    _visiableList.push(childPath.value.length - 1)
  visiableList.value = _visiableList.reverse()
  // 当可见数量和子项数量一致不显示省略号
  if (visiableList.value.length === childPath.value.length) isHide.value = false
}, 0)
const muObserver = new MutationObserver(calcVisiableList)

onMounted(() => {
  muObserver.observe(nav.value!, {
    childList: true,
  })
  setTimeout(() => {
    calcVisiableList()
  }, 0)
})

function hdlClick(name: string, path: string) {
  emit("click", {
    type: "directory",
    filename: path,
    basename: name,
  })
}

function hdlBack() {
  const path = childPath.value.slice(0, visiableList.value[0]).join("/")
  emit("click", {
    type: "directory",
    filename: "/" + path,
  })
}

// 拼接子路径
function joinChildPath(name: string) {
  if (!name) return "/" + childPath.value.join("/")
  return (
    "/" +
      childPath.value
        .slice(0, childPath.value.findIndex((it) => it === name) + 1)
        .join("/") || "/"
  )
}

function getTotalNum(index: number) {
  return index === visiableList.value.length - 1
    ? props.fileTotalNum
    : undefined
}
</script>

<template>
  <div
    class="flex h-[44px] relative items-center"
    :class="{ 'max-w-[90%]': visiableList.length }"
  >
    <BreadcrumbItem
      @click-item="hdlClick"
      :style="!childPath.length && { color: '#333', fontWeight: '600' }"
      :totalNum="!childPath.length ? fileTotalNum : undefined"
      :need-icon="childPath.length"
      :name="rootName || '全部文件'"
      path="/"
    />
    <BreadcrumbItem
      v-if="childPath.length && isHide"
      @click-item="hdlBack()"
      name="..."
      path=""
    />
    <div ref="nav" class="grow flex overflow-x-hidden">
      <BreadcrumbItem
        @click-item="hdlClick"
        v-for="(it, index) in childPath"
        :totalNum="getTotalNum(index)"
        :data-index="index"
        :name="it"
        :path="joinChildPath(it)"
      />
    </div>
    <!-- 当子路径溢出覆盖层 -->
    <div v-if="true && isHide" class="absolute w-full h-full flex bg-white">
      <BreadcrumbItem
        @click-item="hdlClick"
        :style="!childPath.length && { color: '#333', fontWeight: '600' }"
        :need-icon="childPath.length"
        :name="rootName || '全部文件'"
        path="/"
      />
      <BreadcrumbItem
        v-if="childPath.length && isHide"
        @click-item="hdlBack()"
        name="..."
        path=""
      />
      <div class="grow relative flex items-center">
        <div class="absolute w-full flex">
          <BreadcrumbItem
            class="only:w-full"
            @click-item="hdlClick"
            v-for="(it, index) in visiableList"
            :totalNum="getTotalNum(index)"
            :name="childPath[it] || childPath.slice(-1)[0] || ''"
            :path="joinChildPath(childPath[it])"
          />
        </div>
      </div>
    </div>
  </div>
</template>
