import { unref } from "vue"
import {
  createClient,
  type WebDAVClient,
  type FileStat,
} from "@lazycatcloud/webdav/web"
import path from "path-browserify"

import LzcToastCreator from "@/mobile/components/toast/lzcToast"
import { useHistoryTrashStore } from "@/shared/store/historyTrashStore"
import { asyncForEach } from "@/shared/utils/asyncForEach"
import { WEBDAV_URL } from "@/shared/utils/enum"
import { TRASH_ROOT } from "@/config"
import { DiskType } from "../types/type"
import {
  getWebdavRootDir,
  getWebdavUrl,
  getDeviceApiUrl,
} from "@/shared/utils/getClientUrl"
import lzcApiGatewayProxy from "./lzcApiGatewayProxy"

export class LzcWebdavClient {
  client: WebDAVClient | undefined
  prefix: string = ""
  currentDir: string = "/"

  constructor(url: string) {
    if (url.startsWith("http")) {
      const uri = new URL(url)
      this.prefix = decodeURIComponent(uri.pathname)
      url = uri.origin + uri.pathname
    } else {
      this.prefix = decodeURIComponent(url)
    }
    if (this.prefix.endsWith("/")) this.prefix = this.prefix.slice(0, -1)

    this.client = createClient(url, {
      headers: {
        lzc_dapi_auth_token: lzcApiGatewayProxy.authToken,
      },
    })
    this.client.getDirectoryContents
  }

  get isRootDir(): boolean {
    return this.currentDir == "/"
  }

  async getDirectoryContents(dir: string, opts = {}, include_hidden = false) {
    this.currentDir = dir
    let files = (await this.client!.getDirectoryContents(
      dir,
      opts,
    )) as FileStat[]
    files = files.slice(1)
    // 是否包含隐藏文件
    if (!include_hidden)
      files = files.filter((f) => {
        return !f.basename.startsWith(".")
      })
    files.forEach((f) => (f.filename = f.filename.replace(this.prefix, "")))
    return files
  }

  //获取文档类型的内容信息
  async getFileContents(filename: string, opts: object = {}) {
    const fileContents = await this.client!.getFileContents(filename, opts)
    return fileContents as string
  }

  // 通过 filename 获取当前文件的 stat 信息
  async getFileStatByFilename(filename: string) {
    const fileStat = await this.client!.stat(filename)
    return fileStat
  }

  // 获取文件下载链接
  getDownloadLink(filename: string) {
    const downloadLink = this.client!.getFileDownloadLink(filename)
    return downloadLink
  }
  //创建文件夹
  async createDir(name: string) {
    const dirName = unref(name)
    const path = this.currentDir + "/" + dirName
    try {
      await this.client!.createDirectory(path)
    } catch (e: any) {
      if (e.status === 405) {
        throw new Error("已经存在相同的文件夹")
      } else {
        console.error(e)
      }
    }
  }
  // 移动到文件夹, 仅用于在同一个webdav中移动
  async moveFiles(files: FileStat[], dest: string) {
    await asyncForEach(files, async (item: FileStat) => {
      try {
        await this.client!.moveFile(item.filename, dest + "/" + item.basename)
      } catch (e: any) {
        throw new Error(e)
      }
    })
  }
  //复制文件或文件夹
  async copyFiles(files: FileStat[], dest: string) {
    await asyncForEach(files, async (item: FileStat) => {
      try {
        await this.client!.copyFile(item.filename, dest + "/" + item.basename)
      } catch (e: any) {
        throw new Error(e)
      }
    })
  }

  //重命名
  async renameFile(file: FileStat, newName: string, currentPath: string) {
    const src = path.resolve(currentPath, file.basename)
    const dest = path.resolve(currentPath, newName)
    try {
      await this.client!.moveFile(src, dest)
    } catch (e: any) {
      throw new Error(e)
    }
  }

  //判断是否存在dir
  async isExist(path: string) {
    return await this.client!.exists(path)
  }

  async ensureDirExist(path: string) {
    if (!(await this.isExist(path))) {
      try {
        await this.client!.createDirectory(path)
      } catch (e: any) {
        if (e.status === 405) {
          throw new Error("已经存在相同的文件夹")
        } else {
          console.error(e)
        }
      }
    }
  }

  //删除(普通删除,进回收站)
  async deleteFiles(files: FileStat[]) {
    const historyTrashStore = useHistoryTrashStore()
    // //如果没有.Trash,创建.Trash文件夹
    const hasTrash = await this.client!.exists(".Trash")
    if (!hasTrash) await this.createDir(".Trash")
    //移动文件到TRASH_ROOT中,直接移动文件夹就可以把文件夹里面的内容转移出去
    await asyncForEach(files, async (item: FileStat) => {
      //记录从哪删除的数据
      const unixTime = new Date().getTime()
      const trashName = `${item.basename}.${unixTime}`
      await historyTrashStore.putHistoryPath({
        srcPath: item.filename,
        trashPath: "/" + trashName,
      })
      try {
        await this.client?.moveFile(item.filename, TRASH_ROOT + "/" + trashName)
        new LzcToastCreator({ message: "删除成功" }).present()
      } catch (e: any) {
        throw new Error(e)
      }
    })
  }

  //恢复文件
  async recoverFiles(files: FileStat[]) {
    // const historyTrashStore = useHistoryTrashStore()
    // await asyncForEach(files, async (item: FileStat) => {
    //   const getHistoryPath = (await historyTrashStore.getHistoryPath(
    //     item.filename,
    //   )) as any
    //   console.log(getHistoryPath)
    // })
  }

  //彻底删除 deepDeleteFiles
  async deepDeleteFiles(files: FileStat[]) {
    await asyncForEach(files, async (item: FileStat) => {
      try {
        await this.client!.deleteFile(item.filename)
      } catch (e: any) {
        throw new Error(e)
      }
    })
  }

  //tips: 下载文件的时候有两种情况
  //=> 1. 如果下载的内容包含文件夹（需要考虑文件夹内包含文件夹的情况），这时候就需要去递归调用获取文件夹内容的函数，去拆分文件夹
  //=> 2. 只有具体文件内容的时候，
  //主要思路: 维护一个空数组[]。判断传递过来的，先把具体的媒体文件放进去，然后再递归的获取文件内容 ，push 进该数组。
  async walkDirectoryContents(
    dirs: Array<string>,
    fileResults?: Array<FileStat> | undefined,
  ) {
    let files: Array<FileStat> = fileResults == undefined ? [] : fileResults
    await asyncForEach(dirs, async (item: string) => {
      const stat = (await this.client?.stat(item)) as FileStat
      files.push(stat)
      if (stat.type == "file") return
      const results = await this.getDirectoryContents(item, {}, true)
      await asyncForEach(results, async (f: FileStat) => {
        switch (f.type) {
          case "file":
            files.push(f)
            break
          case "directory":
            await this.walkDirectoryContents([f.filename], files)
            break
          default:
        }
      })
    })
    return files
  }
}

export async function getWebdavRootFromType(
  t: DiskType,
): Promise<string | undefined> {
  switch (t) {
    case DiskType.netdisk:
      return WEBDAV_URL
    case DiskType.trashdisk:
      return WEBDAV_URL + TRASH_ROOT
    case DiskType.offline:
      const url = await getWebdavUrl()
      return (
        url.replace(/\/$/, "") +
        path.join(await getWebdavRootDir(), "懒猫云离线")
      )
    // return url.replace(/\/$/, "") + (await getWebdavRootDir()) + "懒猫云离线"
    case DiskType.localdevice:
      return await getDeviceApiUrl()
    default:
      return undefined
  }
}
