<script lang="ts">
export default {
  inheritAttrs: false,
}
</script>
<script setup lang="ts">
import { computed, inject, provide, ref } from "vue"
import {
  VirtualList,
  ScrollBar,
  useSelectableInjectors,
} from "@lazycatcloud/lzc-toolkit"
import dayjs from "dayjs"

import MimeIcon from "@/shared/components/MimeIcon.vue"
import Checkbox from "@/pc/components/Checkbox/index.vue"
import ListHeader from "./components/ListHeader.vue"
import FileInput from "@/pc/components/Dialog/FileFolderPicker/FileInput.vue"
import EmptyDir from "./components/EmptyDir.vue"
import ImageIcon from "@/shared/components/ImageIcon.vue"

import { useWatchScrollTop } from "@/pc/components/FileList/use/useWatchScrollTop"
import { SortType } from "@/shared/use/useFileSort"
import { formatBytes } from "@/shared/utils/formatBytes"

import { getThumbnailUrl, hasThumbnail } from "@/shared/utils/file"
import type { DiskType } from "@/shared/types/type"
import type { FileStat } from "@lazycatcloud/webdav"
import { useDragSelectByFileList } from "./use/useDragSelect"

interface FileCol {
  // 列类型
  type: SortType
  // 文件
  file: FileStat
  // 格式化后的数据
  formatData: string
}

const props = withDefaults(
  defineProps<{
    files: FileStat[]
    actionId?: string
    onlyFilename?: boolean
    showCheckbox?: boolean
    // 是否需要自定义右键菜单
    showMenuList?: boolean
    // 是否需要头部的全选栏
    showSelectBox?: boolean
    mode: DiskType
    // 是否使用默认的 checked 事件
    defaultChecked?: boolean
    // 是否使用默认的全选事件
    defualtSelectedAll?: boolean
    // 是否使用默认的滚动监听事件
    defaultScrollChanged?: boolean
    // 是否允许拖转选择
    canDragSelect?: boolean
  }>(),
  {
    files: () => [],
    actionId: "global",
    showCheckbox: true,
    showMenuList: true,
    defaultChecked: true,
    showSelectBox: true,
    defualtSelectedAll: true,
    defaultScrollChanged: true,
    canDragSelect: true,
  },
)
const emit = defineEmits([
  "dirClick",
  "fileClick",
  "checkboxClick",
  "zipClick",
  "allClick",
])

const vListRef = ref<null | InstanceType<typeof VirtualList>>(null)
//监听vListRef的滚动位置,退出时还原
if (props.defaultScrollChanged) useWatchScrollTop(vListRef)
const { isClick } = useDragSelectByFileList(vListRef, props.canDragSelect)

//处理新建文件夹逻辑
const isCreateNewDir = inject("isCreateNewDir", ref(false))
const createNewDir = inject("createNewDir", (newDirName: string) => {
  newDirName
})
const cancelCreate = () => {
  isCreateNewDir.value = false
}

const files = computed(() => {
  const newArr: FileStat[] = [
    {
      filename: "", // 无需真实
      basename: "", // 无需真实
      lastmod: new Date().toISOString(),
      size: -1,
      type: "directory",
      etag: null,
    },
  ]

  if (isCreateNewDir?.value) {
    return newArr.concat(props.files)
  } else {
    return props.files
  }
})
// 选择逻辑
const { isSelected, isAllSelected, toggle, toggleAll } =
  useSelectableInjectors()

// 选中样式
const itemClass = computed(
  () => (checked: boolean) =>
    Object({
      "item-selected": checked,
      "item-hover": !checked,
    }),
)

// 列宽划分
const gridTemplateColumns = computed(() => {
  return props.onlyFilename
    ? "100%"
    : "minmax(300px,7.4fr)  minmax(180px,1.6fr) minmax(80px,0.5fr) minmax(80px,0.5fr)"
})

provide("$gridInjectStyle", gridTemplateColumns)

// 表头
const colHeaders = computed(() => {
  return props.onlyFilename
    ? [SortType.name]
    : [SortType.name, SortType.time, SortType.size, SortType.type]
})

// 获取该行所有列的数据
const row = computed(() => (file: FileStat): FileCol[] => {
  if (props.onlyFilename) return [getFileCol(SortType.name, file)]
  return colHeaders.value.map((sortType) => getFileCol(sortType, file))
})

// 根据列类型返回数据
function getFileCol(sortType: SortType, file: FileStat) {
  let fileCol: FileCol = { type: sortType, file: file, formatData: "" }
  switch (sortType) {
    case SortType.name:
      fileCol.formatData = file.basename
      break
    case SortType.time:
      fileCol.formatData =
        file.size !== -1 ? dayjs(file.lastmod).format("YYYY-MM-DD HH:mm") : "一"
      break
    case SortType.size:
      // 文件夹不显示大小
      fileCol.formatData =
        file.type === "file" ? formatBytes(file.size, 2) : "一"
      break
    case SortType.type:
      const isHaveSemi = file.mime?.includes(";") //有可能包含分号，分号后面的编码格式是不需要展示的
      const mime = isHaveSemi ? file.mime?.split(";")[0] : file.mime
      fileCol.formatData = mime?.split("/").slice(-1)[0] || "文件夹"
      break
  }
  return fileCol
}

// 发送文件点击和文件夹点击事件
const hdlClickItem = (item: FileStat) => {
  const MIME_ZIP = ["application/zip", "application/x-7z-compressed"]
  if (!isCreateNewDir.value) {
    if (item.mime && MIME_ZIP.includes(item.mime)) {
      emit("zipClick", item)
    } else if (item.type === "directory") {
      emit("dirClick", item)
    } else emit("fileClick", item)
  }
}

const handleClickAllButton = () => {
  if (props.defualtSelectedAll) {
    toggleAll()
  }
  emit("allClick")
}

const hdlClickCheckBox = (file: FileStat) => {
  // 如果组件 prop 中提供了 defaultChecked 属性，意味着需要默认选中该文件，在加载组件时进行切换
  if (props.defaultChecked) toggle([file])
  emit("checkboxClick", file)
}
</script>
<template>
  <EmptyDir v-if="files.length === 0" />
  <div v-else class="flex flex-col grow 1" v-bind="$attrs">
    <!-- 列表头部:内部处理排序逻辑  -->
    <div
      v-if="showSelectBox"
      class="flex items-center border-b-[#F6F6F6] border-b-[1px] w-[calc(100%-12px)]"
    >
      <div
        v-if="showCheckbox"
        @click="handleClickAllButton"
        class="cursor-pointer p-[12px] pl-0 shrink-0"
      >
        <Checkbox :checked="isAllSelected || false" />
      </div>
      <ListHeader :col-headers="colHeaders" v-bind="{ actionId }" />
    </div>
    <div class="relative grow">
      <ScrollBar
        v-if="vListRef"
        :container-el="vListRef.vListReturn.containerProps.ref.value"
        auto-hide
      />
      <!-- 列表主体 格式化渲染数据 -->
      <VirtualList
        ref="vListRef"
        class="text-[14px] text-[#333] absolute left-[-12px]"
        :data="files.slice(0)"
        :item-height="() => 54"
        v-slot="{ index, data: file }"
      >
        <!-- NOTE: 后期如果有需要,可考虑组件方式 -->
        <!-- @click="showCheckbox && toggle([file])" -->
        <div
          :filename="file.filename"
          @mousedown.left="isClick = true"
          @mousemove="isClick = false"
          @mouseup="isClick && hdlClickItem(file)"
          class="pl-[12px] w-full flex flex-col select-none"
          :key="file.basename"
          :class="itemClass(isSelected(file))"
        >
          <!-- 文件信息 -->
          <div
            class="flex items-center grow"
            :class="{
              // XXX(d1y): 设计大佬说分割线都要有:)
              'border-b-[#F6F6F6] border-b-[1px]':
                files.length - 1 !== index || true,
            }"
          >
            <div
              v-if="showCheckbox"
              @click.stop="hdlClickCheckBox(file)"
              @mousedown.stop
              class="h-full flex items-center"
            >
              <Checkbox
                :checked="isSelected(file)"
                class="mr-[12px] shrink-0"
              />
            </div>
            <!-- 列数据 -->
            <div
              class="listView w-full h-full items-center"
              :style="{
                'grid-template-columns': gridTemplateColumns,
              }"
            >
              <div v-for="col in row(file)" class="flex items-center">
                <div
                  v-if="col.type === SortType.name"
                  class="flex items-center w-full"
                >
                  <div
                    class="w-[28px] h-[28px] mr-[20px]"
                    v-if="col.file.mime && hasThumbnail(col.file, mode)"
                  >
                    <ImageIcon
                      :src="getThumbnailUrl(col.file, mode)"
                      :type="col.file.mime"
                    />
                  </div>
                  <MimeIcon
                    v-else
                    class="w-[28px] h-[28px] mr-[20px]"
                    :mime="col.file.mime || col.file.type"
                  />
                  <div
                    v-if="col.file.size == -1"
                    class="w-full h-[22px] flex items-center"
                  >
                    <FileInput
                      :files="files"
                      @input-esc="cancelCreate"
                      @focusout="createNewDir"
                      class="w-[64%] h-[34px]"
                    />
                  </div>

                  <div v-else class="grow relative">
                    <span class="invisible">.</span>
                    <span class="absolute w-full pr-[30px] truncate">
                      {{ col.formatData }}
                    </span>
                  </div>
                </div>

                <span
                  v-else
                  class="shrink-0 w-full truncate"
                  :class="{
                    'text-[#CFCFCF] text-center': col.formatData === '一',
                  }"
                >
                  {{ col.formatData }}
                </span>
              </div>
            </div>
          </div>
        </div>
      </VirtualList>
    </div>
  </div>
</template>

<style scoped>
.listView {
  display: grid;
  grid-column-gap: 10px;
}

/* 深层选择 */
:deep(.listView) {
  display: grid;
  grid-column-gap: 10px;
}
</style>
