<script setup lang="ts">
import { computed, type CSSProperties } from "vue"

import checkboxSVG from "@/icons/选中_PC.svg"

const props = defineProps<{
  checked: boolean
}>()

const checkStyle = computed<CSSProperties>(() => {
  return {
    background: props.checked ? "#2EC1CC" : "",
    border: props.checked ? "" : "1px solid #D9D9D9",
  }
})
</script>
<template>
  <div
    class="flex items-center justify-center w-[12px] h-[12px] rounded-[2px] cursor-pointer"
    :style="checkStyle"
  >
    <img class="w-[6px] h-[6px]" :src="checkboxSVG" />
  </div>
</template>
