<script lang="ts" setup>
import { computed, toRef, inject, ref, type Ref } from "vue"

import Loading from "@/mobile/components/Loading.vue"
import MimeIcon from "@/shared/components/MimeIcon.vue"

import { useActionStore } from "@/shared/store/action"
import useFileSort from "@/shared/use/useFileSort"
import { getThumbnailUrl, hasThumbnail } from "@/shared/utils/file"
import { toChunk } from "@/shared/utils/builder"
import { useSelectableInjectors, VirtualList } from "@lazycatcloud/lzc-toolkit"
import { formatBytes } from "@/shared/utils/formatBytes"
import dayjs from "dayjs"

import "@lazycatcloud/lzc-toolkit/dist/style.css"
import ImageIcon from "@/shared/components/ImageIcon.vue"

import type { FileStat } from "@lazycatcloud/webdav/web"
import { DiskType, modeType } from "@/shared/types/type"

const props = withDefaults(
  defineProps<{
    list?: Array<FileStat>
    emptyText?: string
    showAdd?: boolean
    isLoading?: boolean
    isSort?: boolean
    mode: DiskType
    viewType?: modeType | undefined
  }>(),
  {
    list: () => [],
    emptyText: "什么都没有～",
    showAdd: true,
    isLoading: false,
    isSort: true,
  },
)

const emit = defineEmits<{
  (event: "itemClick", item: FileStat): void
  (event: "addFile"): void
}>()

const isSelectingModeByRoot = inject<Ref<boolean>>(
  "$$isSelectingMode",
  ref(false),
)
const { isSelected } = useSelectableInjectors()
const actionStore = useActionStore()
const viewMode = computed(() => {
  return props.viewType || actionStore.actionMap["global"].viewMode
})

const fileList = toRef(props, "list")

const handleClick = async (item: FileStat) => {
  emit("itemClick", item)
}
const getItemHeight = () => Math.ceil(screen.width / 3) + 41

const list = computed(() => {
  const { sortFileList } = useFileSort(fileList)
  const _fileList = props.isSort ? sortFileList.value : props.list
  if (viewMode.value === modeType.grid) {
    return toChunk(_fileList, 3) //切换大图模式
  }
  return _fileList
})
</script>

<template>
  <div class="grow flex flex-col">
    <Loading v-if="isLoading" />
    <div v-else class="grow overflow-auto">
      <template v-if="list.length > 0">
        <VirtualList
          v-if="viewMode !== modeType.grid"
          :data="list"
          :itemHeight="() => 72"
          v-slot="{ data: item }"
        >
          <div
            :key="item.filename"
            class="w-full flex items-center px-[1rem]"
            v-bind:class="item.type"
            :style="{
              background: isSelected?.(item) ? `#F7F8F9` : '',
              opacity:
                isSelectingModeByRoot && item.type == 'directory' ? '0.6' : '1',
            }"
          >
            <div class="w-full flex items-center" @click="handleClick(item)">
              <div class="w-[36px] h-[36px] overflow-hidden rounded-md">
                <ImageIcon
                  v-if="hasThumbnail(item, mode)"
                  class="rounded-md"
                  :src="getThumbnailUrl(item, mode)"
                  :type="item.mime"
                />
                <MimeIcon
                  v-else
                  class="w-[36px] h-[36px] object-cover rounded-[6px]"
                  :mime="item.mime || item.type"
                />
              </div>
              <div class="px-[15px] max-w-[250px]">
                <div
                  class="break-all overflow-hidden text-ellipsis leading-[20px]"
                  style="
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    line-clamp: 2;
                    -webkit-box-orient: vertical;
                  "
                >
                  <span class="text-[#333333] font-medium text-[14px]">
                    {{ item.basename }}
                  </span>
                </div>
                <div
                  class="flex mb-[2px] h-[14px] text-[10px] font-normal text-[#999999]"
                >
                  <span>{{
                    dayjs(item.lastmod).format("YYYY-MM-DD HH:mm")
                  }}</span>
                  <span class="mx-[8px]" v-if="item.type == 'file'">
                    {{ formatBytes(item.size, 2) }}
                  </span>
                </div>
              </div>
            </div>

            <div class="flex justify-center items-center h-full">
              <slot name="end" :item="item" />
            </div>
          </div>
        </VirtualList>
        <VirtualList
          v-else
          class="text-center"
          :data="list"
          :itemHeight="getItemHeight"
          v-slot="{ data }"
        >
          <div class="px-[20px] grid w-full h-full grid-cols-3 gap-[10px]">
            <div
              class="relative flex flex-col rounded-[6px] items-center border-white border-b-[33px] h-full"
              @click="handleClick(item)"
              :key="item.basename"
              :class="item.type"
              v-for="item in data"
            >
              <div
                class="h-full w-full flex flex-col"
                :style="{
                  background: `${isSelected?.(item) ? `#F7F8F9` : ''}`,
                  borderRadius: `4px`,
                  overflow: 'hidden',
                }"
              >
                <div
                  class="flex flex-col justify-center items-center pt-[16px]"
                >
                  <div class="max-h-[68px] max-w-[68px] flex justify-center">
                    <ImageIcon
                      class="h-[68px] w-[68px] rounded-[6px] object-cover"
                      :src="getThumbnailUrl(item, mode)"
                      :type="item.mime"
                      v-if="hasThumbnail(item, mode)"
                    />
                    <MimeIcon
                      v-else
                      class="h-[68px] w-[68px] rounded-[6px] object-cover"
                      :mime="item.mime || item.type"
                    />
                  </div>

                  <div class="absolute right-[5px] top-[5px]">
                    <slot name="end" :item="item" />
                  </div>

                  <div class="flex flex-col items-center w-[80px]">
                    <div class="baseNameBox">
                      <span class="text-[14px] font-medium">
                        {{ item.basename }}
                      </span>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </VirtualList>
      </template>
      <div
        v-else-if="!isLoading"
        image-size="80"
        class="flex flex-col justify-center items-center h-[80%]"
      >
        <img
          class="pointer-events-none w-[72px]"
          src="@/mobile/assets/images/empty.png"
        />
        <div class="h-[30px]"></div>
        <p class="text-[14px] text-[#A5A5A5]">{{ emptyText }}</p>
        <!-- TODO(d1y): 显示上传(添加文件)按钮 -->
        <button
          v-if="showAdd && false"
          class="flex mt-[12px] items-center justify-center text-white h-[40px] w-[148px] bg-[#2EC1CC] rounded-[24px]"
          @click="$emit('addFile')"
        >
          添加文件
        </button>
        <div class="h-[18vh]"></div>
      </div>
    </div>
  </div>
</template>

<style scoped>
.baseNameBox {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2; /* 这里是超出几行省略 */
  font-weight: 600;
  word-break: break-all;
  text-overflow: ellipsis;
  overflow: hidden;
}
</style>
