<script lang="ts" setup>
import { ref, computed, onMounted, onUnmounted } from "vue"
import type { FileStat } from "@lazycatcloud/webdav"

const props = withDefaults(
  defineProps<{
    files: FileStat[]
  }>(),
  {},
)
const emits = defineEmits(["focusout", "inputEsc"])

const hasSameName = (basename: string, arr: string[]) => {
  const index = arr.findIndex((t) => t === basename)
  if (index === -1) {
    return false
  } else {
    return true
  }
}

const initvalues = computed(() => {
  let initName = "新建文件夹"
  let initNum = 1
  let name = "新建文件夹"
  const arr = props.files.map((item) => item.basename)
  while (hasSameName(name, arr)) {
    name = initName + initNum
    initNum++
  }
  return name
})
const values = ref(initvalues.value)

const inputBox = ref()

function closeInput() {
  emits("focusout", values.value.trim())
}

onMounted(() => {
  document.addEventListener("mousedown", closeInput)
  inputBox.value.focus()
  inputBox.value?.setSelectionRange(0, inputBox.value?.selectionEnd)
})

onUnmounted(() => {
  document.removeEventListener("mousedown", closeInput)
})
</script>
<template>
  <!--  只有window平台会触发keydown.esc,其他平台按Esc会直接失焦 -->
  <input
    ElemInput="ElemInput"
    @mousedown.stop
    @focusout="$emit('inputEsc')"
    @keydown.esc="$emit('inputEsc')"
    @keydown.enter="$emit('focusout', values.trim())"
    ref="inputBox"
    v-model="values"
    type="text"
    class="py-0 m-0 leading-none w-full h-full rounded-[4px]"
  />
</template>
<style scoped>
input {
  outline-style: none;
  border: 1px solid #2ec1cc;
  font-size: 14px;
  color: #333333;
}
input:focus {
  border-color: #2ec1cc;
  outline: 0;
  -webkit-box-shadow: inset 0 0px 0px #2ec1cc, 0 0 0px #2ec1cc;
  box-shadow: inset 0 0px 0px #2ec1cc, 0 0 0px #2ec1cc;
  caret-color: #2ec1cc;
}
</style>
