import { ref, computed, toRaw } from "vue"
import { defineStore } from "pinia"

import tm, { Solution } from "@/shared/api/taskManage"
import { useWebdavStore } from "@/shared/store/webdav"

import {
  TaskType,
  TaskStatus,
  type Task,
  type ExceptionItem,
  type ConflictOption,
  type TaskParams,
} from "@/shared/api/taskManage"
import type { FileStat } from "@lazycatcloud/webdav"
import { DiskType } from "@/shared/types/type"
import { getDirname } from "@/shared/utils/file"
import PCToastCreator from "@/pc/components/Toast/PCToast"
import type { AxiosError } from "axios"

interface TaskParams2 extends Omit<TaskParams, "src"> {
  src: FileStat[]
}

export const useTask = defineStore("task", () => {
  const webdavStore = useWebdavStore()
  // 需要显示的任务
  const showStatusList = [TaskStatus.Running, TaskStatus.Exception]
  // 控制列表是否显示
  const showList = ref(false)
  // 所有任务列表
  const taskList = ref<Task[]>([])
  // 异常任务列表
  const exceptionList = ref<{ id: number; exception: ExceptionItem }[]>([])
  // 需要显示的任务列表
  const activityTasks = computed<Task[]>(() => {
    // return taskList.value
    return taskList.value.filter((it) => showStatusList.includes(it.status))
  })
  // 控制任务列表按钮是否显示
  const showBtn = computed(() => {
    return activityTasks.value.length > 0
  })
  // 判断当前任务列表是否有异常存在
  const isException = computed(() => {
    return exceptionList.value.length > 0
  })

  // 添加一个任务
  async function add(
    taskParams: TaskParams2,
    taskType: TaskType = TaskType.Copy,
  ) {
    if (taskParams.src.length === 0) return
    const res = await tm[taskType]({
      ...taskParams,
      src: taskParams.src.map((it) => it.filename),
    }).catch((e: AxiosError) => {
      new PCToastCreator({
        message: (e.response?.data as string) || "添加任务出错",
        duration: 3000,
      }).present()
    })
    update()
    return res
  }

  // 将任务重置为取消状态
  async function cancel(taskId: number) {
    tm.cancelTask(taskId)
    update()
  }

  // 更新任务列表
  async function update() {
    taskList.value = await tm.getTaskList()
  }

  // 更新任务状态
  function updateStatus(taskId: number, status: TaskStatus) {
    // 当状态修改为运行的时候显示传输列表
    if (status === TaskStatus.Running) {
      setTimeout(() => {
        if (showBtn.value) showList.value = true
      }, 500)
    }
    const taskIndex = taskList.value.findIndex((it) => it.id === taskId)
    if (taskIndex === -1) return update()
    taskList.value[taskIndex].status = status
  }

  // 更新任务进度
  function updateProgress(taskId: number, progress: any) {
    const taskIndex = taskList.value.findIndex((it) => it.id === taskId)
    if (taskIndex === -1) return
    taskList.value[taskIndex].progress = progress
  }

  // 更新任务异常信息
  async function updateException(taskId: number) {
    const res = await tm.getTaskException(taskId)
    const index = exceptionList.value.findIndex((it) => it.id === taskId)
    setTimeout(() => {
      if (index === -1) exceptionList.value.push(res)
      else exceptionList.value[index] = res
    }, 300)
  }

  // 提交异常解决方案
  async function submitException(
    taskId: number,
    exceptions: ConflictOption[],
    solution?: Solution,
  ) {
    await tm.submitException(taskId, exceptions, solution)
    exceptionList.value = exceptionList.value.filter((it) => it.id !== taskId)
  }

  // 当任务结束的时候
  async function onTaskFinished(data: any) {
    await update()
    const task = taskList.value.find((it) => it.id === data.id)
    if (!task) return
    if (task.type === TaskType.Move) {
      if (webdavStore.modeType === DiskType.trashdisk)
        await webdavStore.loadDir("/")
      else if (webdavStore.currentDir === getDirname(task.origin.src[0]))
        webdavStore.loadDir()
    }
  }

  // 初始化
  async function init() {
    // 监听websock 任务状态
    tm.listenTask((ev) => {
      const data = JSON.parse(ev.data)
      if (data.type === "progress") updateProgress(data.id, data.progress)
      else if (data.type === "status" && data.status.action === "add") {
        if (data.status.status === TaskStatus.Exception)
          updateException(data.id)
        else if (data.status.status === TaskStatus.Finished)
          onTaskFinished(data)
        else updateStatus(data.id, data.status.status)
      }
    })
    // 初始化任务列表
    await update()
    // 将冲突任务删除,获取异常
    taskList.value.forEach(async (it) => {
      if (it.status === TaskStatus.Exception) await updateException(it.id)
      else if (it.status === TaskStatus.Conflict) cancel(it.id)
    })
  }
  init()

  return {
    showList,
    showBtn,
    isException,
    exceptionList,
    taskList,
    activityTasks,
    add,
    cancel,
    update,
    submitException,
  }
})
