import type { FileStat } from "@lazycatcloud/webdav"
import { computed, type Ref, type ComputedRef, unref, isRef } from "vue"

const kMagicAllValues = ["false", "all", "*", ""]

export default function $filterFiles(files: FileStat[], rules: string) {
  let ruleTables = [...new Set(rules.split(",").map((item) => item.trim()))]
  for (const magic of kMagicAllValues) {
    if (ruleTables.includes(magic)) return files
  }
  const $rules = ruleTables.map((item) => {
    const tables = item.split("/")
    let matchAll = false
    let matchAllWithEnd = false
    let match = item
    const exactTables = item.split(".")
    if (item.startsWith("*.") && exactTables.length > 1) {
      match = exactTables[1]
      matchAllWithEnd = true
    }
    if (tables.length == 2) {
      matchAll = tables[1] == "*"
      if (matchAll) {
        match = tables[0]
      }
    }
    return {
      match,
      matchAll,
      matchAllWithEnd,
    }
  })
  const lists = files.filter((item) => {
    const { mime, type, filename } = item
    if (type == "directory") return true
    for (let $rule of $rules) {
      const { match, matchAll, matchAllWithEnd } = $rule
      if (matchAll) {
        const isNext = mime?.startsWith($rule.match)
        if (isNext) return true
      }
      if (matchAllWithEnd) {
        const fileSuffix = filename.split(".")[filename.split(".").length - 1]
        const isNext = match == fileSuffix
        if (isNext) return true
      }
      if (mime == match) return true
    }
    return false
  })
  return lists
}

export const useFilter = <T extends FileStat[], R extends string>(
  files: Ref<T> | ComputedRef<T>,
  rules: Ref<R> | ComputedRef<R> | R,
) => {
  const list = computed<FileStat[]>(() => {
    const rule = isRef(rules) ? rules.value : rules
    const result = $filterFiles(files.value, rule)
    return result
  })
  return { list }
}
