import type { MaybeRef } from "@vueuse/shared"
import { ref, unref, watch } from "vue"

/**
 * 用于处理loading 显示过于生硬的问题, 如果 loadingState 变更为true, 只有超过了 delay 后isLoading才会实际变更为true
 *  @param loadingState 传入的原始loading值
 *  @param delay 延迟的时间(ms), 默认为500ms
 * */
export const useDelayedLoading = (
  loadingState: MaybeRef<boolean>,
  delay = 300,
) => {
  const isLoading = ref(false)
  var timerId: number

  watch(
    () => unref(loadingState),
    (current, _) => {
      if (current == true) {
        timerId = window.setTimeout(() => {
          if (unref(loadingState) == true) {
            isLoading.value = true
          }
        }, delay)
      } else {
        if (timerId) window.clearTimeout(timerId)
        isLoading.value = false
      }
    },
  )
  return {
    isLoading,
  }
}
