<script setup lang="ts">
import { ref, onMounted, watch } from "vue"

import { getIconFromMime } from "@/shared/utils/file"

import directoryIcon from "@/icons/default-folder-new.svg"
import fileIcon from "@/icons/application-blank.svg"

const props = withDefaults(
  defineProps<{
    mime: string
  }>(),
  {},
)

const src = ref("")

function getIconByMime() {
  if (__MIME_PLAIN_MODE__ == "true") {
    if (props.mime == "directory") {
      src.value = directoryIcon
    } else {
      src.value = fileIcon
    }
  } else {
    src.value = getIconFromMime(props.mime)
  }
}

// onMounted(() => {
//   if (__MIME_PLAIN_MODE__ == "true") {
//     if (props.mime == "directory") {
//       src.value = directoryIcon
//     } else {
//       src.value = fileIcon
//     }
//   } else {
//     src.value = getIconFromMime(props.mime)
//   }
// })

onMounted(getIconByMime)

watch(() => props.mime, getIconByMime)

function onLoadError() {
  src.value = "/mimetypes-icons/none.svg"
}
</script>
<template>
  <img
    class="pointer-events-none"
    draggable="false"
    :src="src"
    @error="onLoadError"
  />
</template>

