import { defineStore } from "pinia"
import { MiniDB } from "@lazycatcloud/minidb"
import { ref } from "vue"
import { TRASH_ROOT } from "@/config"
import { getBasename, getDirname } from "@/shared/utils/file"
import type { FileStat } from "@lazycatcloud/webdav"
import type { RemoteDB } from "@lazycatcloud/minidb"

export interface HistoryFile {
  _id?: string
  srcPath: string
  trashPath: string
}

/**
 *  存储删除到回收站之前的历史路径,对MiniDB的增删改查
 */
export const useHistoryTrashStore = defineStore("historyTrashStore", () => {
  const db = ref<InstanceType<typeof MiniDB>>()
  const collect = ref<RemoteDB<HistoryFile> | null>(null)

  //初始化数据库
  const init = async () => {
    const uid = sessionStorage.getItem("uid")
    db.value = new MiniDB()
    // 获取数据库的对应的表
    // await db.removeCollection(uid!)
    collect.value = db.value?.getCollection(uid!)
  }

  // 增加数据
  const putHistoryPath = async (
    historyFile: HistoryFile | Array<HistoryFile>,
  ) => {
    if (Array.isArray(historyFile)) {
      await collect.value?.upsert(historyFile)
    } else {
      await collect.value?.upsert(historyFile)
    }
  }

  const clearAllHistoryPath = async () => {
    if (!db.value) return
    const uid = sessionStorage.getItem("uid")
    await db.value.removeCollection(uid!)
    collect.value = db.value.getCollection(uid!)!
  }

  // 删除数据
  const deleteHistoryPath = async (historyFiles?: HistoryFile[]) => {
    if (!historyFiles) return await collect.value?.remove({})
    await collect.value?.remove(historyFiles?.map((it) => it._id))
  }

  // 查数据
  const getHistoryPath = async (trashPath?: string) => {
    if (!trashPath) return await collect.value?.find({}).fetch()
    return await collect.value?.findOne({ trashPath })
  }

  // 获取对应文件的历史记录
  const getHistoryByFiles = async (files: FileStat[]) => {
    const historyFileMap: Record<string, FileStat[]> = {}
    const filePaths = files.map((it) => TRASH_ROOT + it.filename)
    let allHistoryPath = await getHistoryPath()
    if (!Array.isArray(allHistoryPath)) allHistoryPath = []
    for (const historyFile of allHistoryPath) {
      const dst = getDirname(historyFile.srcPath)
      if (!filePaths.includes(historyFile.trashPath)) continue
      if (!historyFileMap[dst]) historyFileMap[dst] = []
      historyFileMap[dst].push({
        filename: historyFile.trashPath,
        basename: getBasename(historyFile.srcPath),
      } as FileStat)
    }
    return {
      historyFileMap,
    }
  }

  init()

  return {
    init,
    putHistoryPath,
    getHistoryPath,
    getHistoryByFiles,
    clearAllHistoryPath,
    deleteHistoryPath,
  }
})
