export interface filenameRuleType {
  conformRules: boolean
  message: string
}
/**
 * 重命名及新建文件夹时,对新文件名的命名规则统一处理
 * @param newName
 * @returns filenameRuleType:{conformRules, message}
 */
export const filenameRule = (newName: string): filenameRuleType => {
  let conformRules: boolean = true
  let message: string = ""
  if (newName) {
    //newName是否以 . 开头
    const hasfirstPoint = /^\..*/.test(newName)
    if (hasfirstPoint) {
      conformRules = false
      message = "文件名不能以 . 作为开头"
      return {
        conformRules,
        message,
      }
    }
    //newName首尾是否含有空格
    // const hasSpace = !/^\S.*\S$|(^\S{0,1}\S$)/.test(newName)
    // if (hasSpace) {
    //   conformRules = false
    //   message = "文件名首尾不能包含空格"
    //   return {
    //     conformRules,
    //     message,
    //   }
    // }
    //newName是否含有特殊字符
    const hasSpecialCode = /[<>|*?,/]/.test(newName)
    if (hasSpecialCode) {
      conformRules = false
      message = "文件名不能包含以下字符,<,>,|,*,?,,/"
      return {
        conformRules,
        message,
      }
    }
    //newName是否超过255字节长度
    const encode = new TextEncoder()
    const encodeLength = encode.encode(newName).length
    if (encodeLength > 240) {
      conformRules = false
      message = "文件名长度不能超过240字节"
      return {
        conformRules,
        message,
      }
    }
  } else {
    //newName为空
    conformRules = false
    message = "文件名不能为空"
  }
  return {
    conformRules,
    message,
  }
}
